import _ from "lodash";
import { storeOptions } from "./default";
import { queryAll } from "@/api";

const tableName = "language_table_column";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["native_name"]
      },
      left_join_tables: [
        {
          name: "table_column",
          no_default_field: true,
          fields: ["name"],
          on: ["id", "language_table_column", "table_column"]
        },
        {
          name: "table",
          no_default_field: true,
          fields: ["name"],
          on: ["id", "table_column", "table"]
        }
      ]
    }
  }
});

options.state = {
  ...options.state,
  values: {}
};

options.mutations = {
  ...options.mutations,
  setValues(state, values) {
    state.values = values;
  }
};

options.actions = {
  ...options.actions,
  get({ commit, dispatch }, localeId) {
    dispatch("getAll", {
      table: { filters: [[["locale", "=", localeId]]] }
    }).then(({ data }) => {
      const values = {};
      _.forEach(data, item => {
        values[`${item.table.name}.${item.table_column.name}`] =
          item.native_name;
      });
      commit("setValues", values);
    });
  }
};

export default {
  namespaced: true,
  ...options
};
