<template>
  <div>
    <Edit
      :permission="permission"
      :tableName="$route.params.tableName"
      :id="$route.query.id * 1"
      :visible="!!$route.query.showEdit"
      @close="closeEdit"
    />

    <Detail
      :permission="permission"
      :tableName="$route.params.tableName"
      @close="closeEdit"
      @edit="showEdit({ id: $route.query.id * 1 })"
    />

    <RolePermission v-if="showOperation" ref="rolePermission" />
    <MemberPermission v-if="showOperation" ref="memberPermission" />

    <a-card :bordered="false" style="margin:16px 24px">
      <Table
        v-if="permission.list"
        storeModule="list"
        :tableName="$route.params.tableName"
        :permission="permission"
        @show-edit="showEdit"
        @show-detail="showDetail"
      >
        <template v-if="showOperation" v-slot:operation="{ record }">
          <a-button icon="safety" size="small" @click="showPermission(record)">
            {{ languageBase.permission }}
          </a-button>
        </template>
      </Table>
    </a-card>
  </div>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { getAccountTokenId } from "@/utils";
import Table from "@/components/Table.vue";
import Edit from "@/components/Edit.vue";
import Detail from "@/components/Detail.vue";
import MemberPermission from "@/components/PermissionMember.vue";
import RolePermission from "@/components/PermissionRole.vue";

export default {
  name: "ListPage",
  components: {
    Table,
    Edit,
    Detail,
    MemberPermission,
    RolePermission
  },
  data() {
    return {
      uid: getAccountTokenId()
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      permissionLoading: store => store.permission.loading,
      permissions: store => store.permission.values,
      tables: store => _.keyBy(store.table.all.data, "name"),
      allTableColumn: store => store.table_column.all.data,
      system: store => store.user_system.detail.data
    }),
    tableColumns() {
      return _.keyBy(
        _.filter(this.allTableColumn, {
          table: { name: this.$route.params.tableName }
        }),
        "name"
      );
    },
    permission() {
      if (
        this.permissionLoading ||
        _.isEmpty(this.tables) ||
        _.isEmpty(this.system) ||
        _.isEmpty(this.tableColumns)
      )
        return {};

      const { tableName } = this.$route.params;
      const isMember =
        this.system.user.id === this.uid && tableName === "member";

      if (_.isEmpty(this.permissions) && !isMember) return {};

      const table = this.tables[tableName];
      if (!table) return {};

      let permission = this.permissions[table.id];

      if (isMember) {
        permission = {
          ...permission,
          list: {
            ..._.get(permission, "list"),
            fields: _.uniq([
              ...(_.get(permission, "list.fields") || []),
              this.tableColumns.uid.id
            ])
          }
        };
      }

      if (permission.update && permission.detail) {
        permission.update.fields = _.intersection(
          permission.update.fields,
          permission.detail.fields
        );

        permission.update.defaultFields = _.intersection(
          permission.update.defaultFields,
          permission.detail.defaultFields
        );

        permission.update = _.omitBy(permission.update, _.isEmpty);
        if (_.isEmpty(permission.update)) {
          permission = _.omit(permission, ["update"]);
        }
      } else {
        permission = _.omit(permission, ["update"]);
      }

      return permission || {};
    },
    showOperation() {
      if (_.isEmpty(this.system)) return false;

      return (
        (this.permission.permission === 1 ||
          this.system.user.id === this.uid) &&
        /^(member|role)$/.test(this.$route.params.tableName)
      );
    }
  },
  created() {
    this.getAllTableColumn();
  },
  methods: {
    ...mapActions("table_column", {
      getAllTableColumn: "getAll"
    }),
    showEdit({ id } = {}) {
      if (this.$route.params.tableName === "member" && !id) {
        this.$message.info({ content: "邀请成员功能待发开中" });
      } else {
        this.$router.push({
          name: this.$route.name,
          params: this.$route.params,
          query: { id, showEdit: 1 }
        });
      }
    },
    showDetail({ id }) {
      this.$router.push({
        name: this.$route.name,
        params: this.$route.params,
        query: { id, showDetail: 1 }
      });
    },
    closeEdit() {
      this.$router.push({
        name: this.$route.name,
        params: this.$route.params
      });
    },
    showPermission(record) {
      const { tableName } = this.$route.params;
      if (tableName === "member") {
        this.$refs.memberPermission.show(record);
      } else if (tableName === "role") {
        this.$refs.rolePermission.show(record);
      }
    }
  }
};
</script>
