<template>
  <div v-if="value" style="height: 104px">
    <a-upload
      disabled
      list-type="picture-card"
      :file-list="fileList"
      @preview="previewVisible = true"
    />
    <a-modal
      :visible="previewVisible"
      :footer="null"
      :width="params.width + 48"
      @cancel="previewVisible = false"
    >
      <img style="width: 100%" :src="value | cdn" />
    </a-modal>
  </div>
</template>

<script>
export default {
  name: "data-show-image",
  props: {
    value: String,
    params: Object
  },
  data() {
    return {
      previewVisible: false
    };
  },
  computed: {
    fileList() {
      return [
        {
          url: this.$options.filters.cdn(this.value),
          status: "done",
          uid: this.value,
          name: this.value + ".webp"
        }
      ];
    }
  }
};
</script>
