<template>
  <span @click="initList">
    <a-select
      allow-clear
      show-search
      v-model="val"
      mode="multiple"
      :default-active-first-option="false"
      :filter-option="false"
      :loading="fetching"
      :placeholder="languageBase.please_select"
      @select="select"
      @deselect="deselect"
      @search="handleSearch"
      @popupScroll="popupScroll"
      style="width: 100%"
    >
      <a-select-option
        v-for="(item, index) in list.data"
        :key="index"
        :value="item.id"
      >
        {{ item[column.name] }}
      </a-select-option>
      <a-select-option v-if="fetching" value="fetching" disabled>
        <a-spin size="small" />
      </a-select-option>
    </a-select>
  </span>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { queryList } from "@/api";

export default {
  name: "data-entry-multipleSelect",
  props: {
    value: Array,
    params: Object
  },
  data() {
    this.handleSearch = _.debounce(this.handleSearch, 500);

    return {
      val: this.value,
      fetching: true,
      list: { data: [] },
      selected: []
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id"),
      allTableRelated: store => _.keyBy(store.table_related.all.data, "id")
    }),
    column() {
      if (_.isEmpty(this.allTableRelated)) return {};
      const tr = this.allTableRelated[this.params.table_related_id];
      const columnId = tr[`t${this.params.index}_column`].id;
      return this.allTableColumn[columnId];
    }
  },
  watch: {
    value(val) {
      if (!_.isEqual(val, this.val)) {
        this.val = val;
      }
    },
    val(val) {
      if (!_.isEqual(val, this.value)) {
        this.$emit("input", val);
      }
      if (_.isEmpty(val)) {
        this.selected = [];
      }
    }
  },
  mounted() {
    if (_.isEmpty(this.allTableRelated)) {
      this.getTableRelated().then(() => {
        this.fetching = false;
      });
    } else {
      this.fetching = false;
    }

    if (this.params.options) {
      this.list.data = [...this.params.options];
      this.selected = [...this.params.options];
    }
  },
  methods: {
    ...mapActions("table_related", {
      getTableRelated: "getAll"
    }),
    select(id) {
      this.selected.push(_.find(this.list.data, { id }));
    },
    deselect(id) {
      _.pullAllBy(this.selected, [{ id }], "id");
    },
    fetchList(params) {
      this.fetching = true;
      this.fetchList.params = params;

      if (params.current === 1) {
        this.list = { data: [] };
      }

      queryList(params)
        .then(res => {
          if (params === this.fetchList.params) {
            if (res.current === 1) {
              this.list = {
                ...res,
                pages: _.ceil(res.total / res.pageSize)
              };
            } else {
              this.list = {
                ...this.list,
                current: res.current,
                data: [...this.list.data, ...res.data]
              };
            }
          }
        })
        .finally(() => {
          if (params === this.fetchList.params) {
            this.fetching = false;
          }
        });
    },
    handleParams(searchValue) {
      const params = {
        current: 1,
        table: {
          name: this.column.table.name,
          no_default_field: true,
          fields: ["id", this.column.name],
          order_by: [[this.column.name, "ASC"]],
          filters: [[["state", "=", 1]]]
        }
      };

      if (searchValue) {
        params.table.filters = [
          [
            ["state", "=", 1],
            [this.column.name, "LIKE", `%${searchValue}%`]
          ]
        ];
      }

      return params;
    },
    handleSearch(value) {
      !_.isEmpty(this.column) && this.fetchList(this.handleParams(value));
    },
    initList() {
      !this.fetching &&
        _.isEmpty(this.val) &&
        !this.initList.initialized &&
        !this.params.disabled &&
        this.fetchList(this.handleParams());
    },
    popupScroll(e) {
      if (this.fetching) return;

      const { scrollTop, clientHeight, scrollHeight } = e.target;

      if (
        scrollTop + clientHeight + 1 > scrollHeight &&
        this.list.pages > this.list.current
      ) {
        this.fetchList({
          ...this.fetchList.params,
          current: this.list.current + 1
        });
      }
    }
  }
};
</script>
