<template>
  <a-upload disabled :file-list="fileList" />
</template>

<script>
import _ from "lodash";

export default {
  name: "data-show-upload",
  props: {
    value: String
  },
  data() {
    return {
      fileList: []
    };
  },
  watch: {
    value() {
      this.handleList();
    }
  },
  mounted() {
    this.handleList();
  },
  methods: {
    handleList() {
      try {
        this.fileList = _.map(JSON.parse(this.value), file => ({
          ...file,
          url: this.getUrl(file),
          uid: `${this._uid}-${file.hash}`,
          status: "done"
        }));
      } catch (err) {
        this.fileList = [];
      }
    },
    getUrl(file) {
      return this.$options.filters.file(file);
    }
  }
};
</script>
