<template>
  <a-layout-sider
    v-model="collapsed"
    :collapsible="collapsible"
    @collapse="handleCollapse"
  >
    <a-row
      type="flex"
      align="middle"
      justify="center"
      style="height:64px;flex-flow: nowrap"
    >
      <img alt="OOS logo" src="@/assets/logo.svg" style="height:50px" />
      <h3 class="logo-text">
        OOS
      </h3>
    </a-row>

    <a-menu
      v-if="menus.length"
      theme="dark"
      :default-selected-keys="selectedKeys"
      :default-open-keys="openKeys"
      mode="inline"
    >
      <template v-for="item in menus">
        <a-sub-menu v-if="item.children" :key="item.name">
          <span slot="title">
            <a-icon :type="item.icon || 'menu'" />
            <span>{{ item.title }}</span>
          </span>
          <template v-for="item in item.children">
            <a-menu-item :key="item.name">
              <router-link
                :to="{ name: 'list', params: { tableName: item.name } }"
                :title="item.title"
                class="menu-link"
              >
                <a-icon :type="item.icon || 'table'" />
                <span>{{ item.title }}</span>
              </router-link>
            </a-menu-item>
          </template>
        </a-sub-menu>
        <a-menu-item v-else :key="item.name">
          <router-link
            :to="{ name: 'list', params: { tableName: item.name } }"
            :title="item.title"
            class="menu-link"
          >
            <a-icon :type="item.icon || 'table'" />
            <span>{{ item.title }}</span>
          </router-link>
        </a-menu-item>
      </template>
    </a-menu>
  </a-layout-sider>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { getAccountTokenId } from "@/utils";

export default {
  props: {
    collapsible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      uid: getAccountTokenId(),
      collapsed: !this.collapsible
        ? false
        : document.body.offsetWidth < 992 && document.body.offsetWidth > 768,
      selectedKeys: [],
      openKeys: []
    };
  },
  computed: {
    ...mapState({
      languageTable: store => store.languageTable.values,
      languageMenuGroup: store => store.languageMenuGroup.values,
      permission: store => store.permission.values,
      permissionLoading: store => store.permission.loading,
      tables: store => store.table.all.data,
      system: store => store.user_system.detail.data
    }),
    menus() {
      if (
        this.permissionLoading ||
        _.isEmpty(this.system) ||
        _.isEmpty(this.tables) ||
        (this.system.user.id !== this.uid && _.isEmpty(this.permission))
      ) {
        return [];
      }

      const tables = _.filter(this.tables, item => {
        return (
          // 列表权限
          !_.isEmpty(_.get(this.permission, `${item.id}.list`)) ||
          // 系统所属人必有成员管理权限
          (this.system.user.id === this.uid && item.name === "member")
        );
      });

      const menus = _.map(tables, item => ({
        ..._.pick(item, ["name", "icon", "menu_group"]),
        title: this.languageTable[item.name] || item.name
      }));
      const menuGroup = _.groupBy(menus, "menu_group.id");

      return [
        // 无2级菜单
        ...(menuGroup.null || []),
        // 有2级菜单
        ..._.map(_.omit(menuGroup, ["null"]), item => ({
          ..._.pick(item[0].menu_group, ["name", "icon"]),
          title:
            this.languageMenuGroup[item[0].menu_group.name] ||
            item[0].menu_group.name,
          children: item
        }))
      ];
    }
  },
  watch: {
    "$route.name"(val) {
      this.selectedKeys = [val];
      if (val === "list") {
        this.selectedKeys = [this.$route.params.tableName];
      }
    },
    "$route.params.tableName"(val) {
      if (this.$route.name === "list") {
        this.selectedKeys = [val];
      }
    },
    menus() {
      if (!_.isEmpty(this.menus) && _.isEmpty(this.selectedKeys)) {
        const { path, name, params } = this.$route;
        let { tableName } = params;
        let menu;

        // 不是通用列表页 && 不是首页，
        if (name !== "list" && path !== "/") {
          tableName = name;
        }

        if (tableName) {
          // 检查权限，获取菜单
          menu = _.find(this.menus, menuItem =>
            menuItem.children
              ? _.findIndex(
                  menuItem.children,
                  item => item.name === tableName
                ) !== -1
              : menuItem.name === tableName
          );
        }

        // 未获取菜单
        if (_.isEmpty(menu)) {
          menu = this.menus[0];
          if (menu.children) {
            tableName = menu.children[0].name;
          } else {
            tableName = menu.name;
          }
        }

        if (menu.children) {
          this.openKeys = [menu.name];
        }

        this.selectedKeys = [tableName];

        if (
          (name === "list" && params.tableName !== tableName) ||
          (name !== "list" && name !== tableName)
        ) {
          this.$router.replace({ name: "list", params: { tableName } });
        }
      }
    }
  },
  created() {
    this.fetchPermission();
    this.getAllTable();
  },
  mounted() {
    this.handleCollapse();
  },
  methods: {
    ...mapActions("permission", {
      fetchPermission: "fetch"
    }),
    ...mapActions("table", {
      getAllTable: "getAll"
    }),
    handleCollapse() {
      this.$emit("collapse", this.collapsed);
    }
  }
};
</script>

<style lang="stylus">
.ant-layout-sider
  .logo-text
    margin 0 0 0 8px
    width 80px
    opacity 1
    transition all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1)
    white-space nowrap
    color #fff
  .menu-link
    text-overflow ellipsis
    overflow hidden

.ant-layout-sider-collapsed
  .logo-text
    margin 0
    width 0
    opacity 0
</style>
