import _ from "lodash";
import moment from "moment";

const customizer = (objValue, srcValue) =>
  !_.isArray(objValue) ? undefined : objValue.concat(srcValue);

const stateAll = {
  params: {},
  data: [],
  loading: false
};

const stateList = {
  params: {},
  data: [],
  pagination: {},
  loading: false
};

const stateDetail = {
  params: {},
  data: {},
  loading: false
};

const setLoading = (state, { type, loading }) => {
  state[type].loading = loading;
};

const setAllLoading = (state, loading) => {
  state.all.loading = loading;
};

const setListLoading = (state, loading) => {
  state.list.loading = loading;
};

const setDetailLoading = (state, loading) => {
  state.detail.loading = loading;
};

const setData = (
  state,
  { type, data, pageSize, current, total, params, extraData }
) => {
  state[type].params = params;
  state[type].extraData = extraData;

  if (/^(list|all)$/.test(type)) {
    state[type].data = _.map(data, item => ({
      ...item,
      saveLoading: false,
      deleteLoading: false
    }));

    if (type === "list" && current) {
      state.list.pagination = {
        pageSize,
        total: current === 1 ? total : state.list.pagination.total,
        current,
        showSizeChanger: true
      };
    }
  } else {
    state.detail.data = data[0] || {};
  }
};

const setItem = (state, item) => {
  item.id &&
    _.forEach(["all", "list"], type => {
      if (state[type]) {
        const index = _.findIndex(state[type].data, { id: item.id });
        if (index > -1) {
          const ollItem = state[type].data[index];
          _.forEach(item, (value, key) => {
            if (_.isObject(ollItem[key]) && _.isNumber(value)) {
              item[key] = {
                ...ollItem[key],
                id: value
              };
            }
          });
          state[type].data[index] = {
            ...state[type].data[index],
            ..._.cloneDeep(item)
          };
          state[type].data = _.filter(
            state[type].data,
            item => !item.deleted_at
          );
        }
      }
    });
};

const createGetDataAction = config => ({ commit }, { type, params }) => {
  commit("setLoading", { type, loading: true });
  commit("setData", { type, data: [], params: {} });
  return config[type]
    .api(
      _.mergeWith(
        _.cloneDeep(config[type].params),
        _.cloneDeep(params),
        customizer
      )
    )
    .then(async res => {
      config[type].beforeSet && (await config[type].beforeSet(res));
      commit("setData", { ...res, type, params: _.cloneDeep(params) });
      return res;
    })
    .finally(() => {
      commit("setLoading", { type, loading: false });
    });
};

const createGetAction = type => ({ dispatch }, params) =>
  dispatch("getData", { type, params });

const createSaveAction = ({ api }) => (
  { commit, state, dispatch },
  { tableName, record }
) => {
  commit("setItem", { id: record.id, saveLoading: true });
  state.detail && commit("setDetailLoading", true);

  return api({
    table: { name: tableName },
    rows: [{ ...record }]
  })
    .then(() => {
      if (!record.id) {
        state.list &&
          !_.isEmpty(state.list.params) &&
          dispatch("getList", { ...state.list.params, current: 1, sync: 1 });
      } else {
        commit("setItem", {
          ...record,
          updated_at: moment().unix(),
          saveLoading: false
        });
      }
    })
    .catch(err => {
      commit("setItem", { id: record.id, saveLoading: false });
      return Promise.reject(err);
    })
    .finally(() => {
      state.detail && commit("setDetailLoading", false);
    });
};

/** Class representing a store options */
export class storeOptions {
  /**
   * Create a store options
   * @param {Promise}  all.api        - 获取全部数据接口
   * @param {Object}   all.params     - 发送给接口的参数，详情请查看接口文档
   * @param {Function} all.beforeSet  - 设置数据之前执行，beforeSet(接口参数)
   * @param {Promise}  list.api       - 获取分页列表数据接口
   * @param {Object}   list.params    - 发送给接口的参数，详情请查看接口文档
   * @param {Function} list.beforeSet - 设置数据之前执行，beforeSet(接口参数)
   * @param {Promise}  detail.api     - 获取单条数据接口，跟all.api相同
   * @param {Object}   detail.params  - 发送给接口的参数，详情请查看接口文档
   * @param {Promise}  save.api       - 保存接口
   */
  constructor({ all, list, detail, save }) {
    this.state = this.createState({ all, list, detail });
    this.mutations = this.createMutations({ all, list, detail });
    this.actions = this.createActions({ all, list, detail, save });
  }
  createState({ all, list, detail }) {
    const state = {};
    if (all) {
      state.all = _.cloneDeep(stateAll);
    }
    if (list) {
      state.list = _.cloneDeep(stateList);
    }
    if (detail) {
      state.detail = _.cloneDeep(stateDetail);
    }
    return state;
  }
  createMutations({ all, list, detail }) {
    const mutations = {};
    if (all || list || detail) {
      mutations.setLoading = setLoading;
      mutations.setData = setData;

      if (all) {
        mutations.setAllLoading = setAllLoading;
      }
      if (list) {
        mutations.setListLoading = setListLoading;
      }
      if (detail) {
        mutations.setDetailLoading = setDetailLoading;
      }
      if (all || list) {
        mutations.setItem = setItem;
      }
    }
    return mutations;
  }
  createActions({ all, list, detail, save }) {
    const actions = {};
    if (all || list || detail) {
      actions.getData = createGetDataAction({ all, list, detail });

      if (all) {
        actions.getAll = createGetAction("all");
      }
      if (list) {
        actions.getList = createGetAction("list");
      }
      if (detail) {
        actions.getDetail = createGetAction("detail");
      }
    }
    if (save) {
      actions.save = createSaveAction(save);
    }
    return actions;
  }
}
