<template>
  <a-config-provider :locale="locale">
    <a-layout style="min-height:100vh">
      <Sider
        v-if="!miniSider"
        @collapse="collapsed => (visiblePageTitle = collapsed)"
      />

      <template v-else>
        <a-drawer
          placement="left"
          :closable="false"
          :visible="visibleSiderDrawer"
          :width="200"
          :bodyStyle="{
            display: 'flex',
            padding: 0,
            minHeight: '100vh'
          }"
          @close="visibleSiderDrawer = false"
        >
          <Sider :collapsible="false" />
        </a-drawer>
      </template>

      <a-layout>
        <Header :mini="miniSider">
          <a
            v-show="miniSider"
            class="sider-unfold"
            @click.prevent="visibleSiderDrawer = true"
          >
            <a-icon type="menu-unfold" />
          </a>

          <template v-slot:title>
            <span class="ant-page-header-heading-title">{{ pageTitle }}</span>
          </template>
        </Header>

        <a-layout-content>
          <router-view />
          <a-back-top />
        </a-layout-content>
        <a-layout-footer style="text-align: center">
          OOS CC v.{{ APP_VERSION }} ©2020 Created by
          <a :href="'//' + DOMAIN" target="_blank">
            {{ DOMAIN }}
          </a>
        </a-layout-footer>
      </a-layout>
    </a-layout>
  </a-config-provider>
</template>

<script>
import { mapState, mapActions } from "vuex";
import _ from "lodash";
import moment from "moment";
import { DOMAIN } from "@/dict";
import Header from "./Header.vue";
import Sider from "./Sider.vue";

export default {
  name: "Main",
  components: {
    Header,
    Sider
  },
  data() {
    const miniSider = document.body.offsetWidth <= 768;
    return {
      DOMAIN,
      APP_VERSION: process.env.VUE_APP_VERSION,
      locale: _.cloneDeep(window.antd.locales.zh_CN),
      visibleSiderDrawer: true,
      miniSider,
      visiblePageTitle: miniSider
    };
  },
  computed: {
    ...mapState({
      localeCode: store => store.locale.selected.code,
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      system: store => store.user_system.detail.data
    }),
    pageTitle() {
      if (!this.visiblePageTitle) return "";
      let title = this.$route.name;
      if (title === "list") {
        title =
          this.languageTable[this.$route.params.tableName] ||
          this.$route.params.tableName;
      } else {
        title = this.languageTable[title] || this.languageBase[title] || title;
      }
      return "- " + title;
    }
  },
  watch: {
    localeCode() {
      this.handleLocale();
    },
    $route() {
      this.visibleSiderDrawer = false;
    }
  },
  created() {
    this.getSystemDetail();
    this.handleLocale();
  },
  mounted() {
    setTimeout(() => {
      this.visibleSiderDrawer = false;
    }, 1000);
  },
  methods: {
    ...mapActions("user_system", {
      getSystemDetail: "getDetail"
    }),
    handleLocale() {
      if (this.localeCode) {
        this.locale = _.cloneDeep(
          window.antd.locales[this.localeCode] || window.antd.locales.zh_CN
        );

        moment.locale(this.locale.locale);
      }
    }
  },
  metaInfo() {
    return {
      htmlAttrs: {
        lang: this.locale.locale
      },
      title:
        this.system.name && this.$route.path !== "/"
          ? `${this.languageTable[this.$route.params.tableName] ||
              this.languageTable[this.$route.name] ||
              this.$route.params.tableName ||
              this.$route.name} - ${this.system.name}`
          : this.$route.name
    };
  }
};
</script>

<style lang="stylus">
.sider-unfold
  display flex
  align-items center
  justify-content center
  width 64px
  height 64px
  font-size 32px
</style>
