<script>
import Input from "./input";
import InputNumber from "./inputNumber";
import Switch from "./switch";
import Textarea from "./textarea";
import RadioSelect from "./radioSelect";
import MultipleSelect from "./multipleSelect";
import Checkbox from "./checkbox";
import Cascader from "./cascader";
import Radio from "./radio";
import Rate from "./rate";
// import Slider from "./slider";
import DatePicker from "./datePicker";
import Image from "./image";
import Images from "./images";
// import Year from "./year";
// import MonthPicker from "./monthPicker";
import RangePicker from "./rangePicker";
import Upload from "./upload";
import Avatar from "./avatar";
import RichText from "./richText";
import User from "./user";

const Components = {
  [Input.name]: Input,
  [InputNumber.name]: InputNumber,
  [Switch.name]: Switch,
  [Textarea.name]: Textarea,
  [RadioSelect.name]: RadioSelect,
  [MultipleSelect.name]: MultipleSelect,
  [Checkbox.name]: Checkbox,
  [Cascader.name]: Cascader,
  [Radio.name]: Radio,
  [Rate.name]: Rate,
  // [Slider.name]: Slider,
  [DatePicker.name]: DatePicker,
  [Image.name]: Image,
  [Images.name]: Images,
  // [Year.name]: Year,
  // [MonthPicker.name]: MonthPicker,
  [RangePicker.name]: RangePicker,
  [Upload.name]: Upload,
  [Avatar.name]: Avatar,
  [RichText.name]: RichText,
  [User.name]: User
};

export default {
  props: {
    value: [String, Number, Boolean, Array, Object],
    componentName: String,
    params: Object
  },
  data() {
    return {
      val: undefined
    };
  },
  watch: {
    value() {
      this.formatVal();
    },
    componentName() {
      this.formatVal();
    }
  },
  created() {
    this.formatVal();
  },
  render(h) {
    if (!this.componentName) return h();

    return h(Components["data-entry-" + this.componentName], {
      props: {
        value: this.val,
        params: this.params
      },
      on: {
        input: val => {
          this.$emit("input", val);
          this.$emit("change", val);
        },
        blur: val => {
          this.$emit("blur", val);
        }
      }
    });
  },
  methods: {
    formatVal() {
      let val = this.value;
      const type = {}.toString
        .call(val)
        .match(/\s([a-zA-Z]+)/)[1]
        .toLowerCase();

      if (
        /^(input|textarea|upload|avatar)$/.test(this.componentName) &&
        type !== "string"
      ) {
        val = "";
      } else if (
        /^(inputNumber|radioSelect|radio|rate|datePicker|year|monthPicker)$/.test(
          this.componentName
        ) &&
        type !== "number"
      ) {
        val = undefined;
      } else if (
        /^(multipleSelect|checkbox)$/.test(this.componentName) &&
        type !== "array"
      ) {
        val = [];
      }
      this.val = val;
    }
  }
};
</script>
