<template>
  <a-switch
    v-model="val"
    :checked-children="params.checkedChildren"
    :un-checked-children="params.unCheckedChildren"
    :disabled="params.disabled"
    :auto-focus="params.autoFocus"
    @blur="$emit('blur', val)"
  />
</template>

<script>
export default {
  name: "data-entry-switch",
  props: {
    value: [Boolean, Number],
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      val: !this.value === false
    };
  },
  watch: {
    value(val) {
      this.val = val;
    },
    val(val) {
      this.$emit("input", val);
    }
  }
};
</script>
