<template>
  <div>
    <a-upload
      multiple
      list-type="picture-card"
      accept="image/jpeg, image/jpg, image/png, image/webp"
      :action="action"
      :file-list="fileList"
      :before-upload="beforeUpload"
      :data="handleData"
      @change="handleChange"
      @preview="handlePreview"
    >
      <a-icon type="plus" />
      <div class="ant-upload-text">
        {{ params.width }} x {{ params.height }}
      </div>
    </a-upload>
    <a-modal
      :visible="previewVisible"
      :footer="null"
      :width="params.width + 48"
      @cancel="previewVisible = false"
    >
      <img style="width: 100%" :src="previewImage" />
    </a-modal>
  </div>
</template>

<script>
import _ from "lodash";
import { fileH, fileToUrl } from "@/utils";
import { origins } from "@/dict";
import { handleImage } from "./utils/upload";

export default {
  name: "data-entry-images",
  props: {
    value: String,
    params: Object
  },
  data() {
    let fileList = [];
    if (this.value) {
      let hashs = [];
      try {
        hashs = JSON.parse(this.value);
        hashs = _.filter(hashs, hash => !!hash);
      } catch (err) {
        // no todo
      }

      fileList = _.map(hashs, hash => ({
        hash,
        url: this.$options.filters.cdn(hash),
        status: "done",
        uid: hash,
        name: hash + ".webp"
      }));
    }
    return {
      action: `${origins.OSS}/upload`,
      fileList,
      previewVisible: false,
      previewImage: ""
    };
  },
  watch: {
    fileList(val) {
      this.$emit("input", JSON.stringify(_.map(val, item => item.hash)));
    }
  },
  methods: {
    beforeUpload(file) {
      if (!/^image\/(jpeg|jpg|png|webp)$/.test(file.type)) {
        this.$message.error("只能上传jpg、jpg、png、webp格式图片");
        return false;
      }

      const { uid, name } = file;

      return new Promise((resolve, reject) => {
        handleImage({ ...this.params, file })
          .then(file => {
            fileToUrl(file)
              .then(src => {
                file.url = src;
                file.uid = uid;
                file.name = name;

                if (file.status === "done") {
                  this.fileList.push(file);
                  this.fileList = _.uniqBy(this.fileList, "hash");
                  return reject();
                }

                resolve(file);
              })
              .catch(reject);
          })
          .catch(reject);
      });
    },
    handleData(file) {
      return {
        h: file.hash,
        s: fileH(file.hash).toString()
      };
    },
    handleChange({ fileList }) {
      this.fileList = _.uniqBy(fileList, "hash");
    },
    handlePreview(file) {
      this.previewImage = file.url;
      this.previewVisible = true;
    }
  }
};
</script>
