<template>
  <span @click="initList">
    <a-select
      allow-clear
      show-search
      v-model="val"
      :default-active-first-option="false"
      :filter-option="false"
      :loading="fetching"
      :placeholder="languageBase.please_select"
      :auto-focus="params.autoFocus"
      :disabled="params.disabled"
      @search="handleSearch"
      @popupScroll="popupScroll"
      style="width: 100%"
    >
      <a-select-option
        v-for="(item, index) in list.data"
        :key="index"
        :value="item.id"
      >
        {{ item[column.name] }}
      </a-select-option>
      <a-select-option v-if="fetching" value="fetching" disabled>
        <a-spin size="small" />
      </a-select-option>
    </a-select>
  </span>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { queryList } from "@/api";

export default {
  name: "data-entry-radioSelect",
  props: {
    value: [Number, String],
    params: Object
  },
  data() {
    this.handleSearch = _.debounce(this.handleSearch, 500);

    return {
      val: this.value,
      fetching: false,
      list: { data: [] }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id")
    }),
    column() {
      return this.allTableColumn[this.params.table_column_id];
    },
    selected() {
      return _.find(this.list.data, { id: this.val });
    }
  },
  watch: {
    value(val) {
      if (val !== this.val) {
        this.val = val;
        if (val !== undefined) {
          this.list.data = [...this.params.options];
        }
      }
    },
    val(val) {
      if (val === undefined) {
        this.initList.initialized = false;
      }

      if (val !== this.value) {
        this.$emit("input", val);
        this.$emit("change", val, this.selected);
      }
    },
    params(val, old) {
      if (!_.isEqual(val, old)) {
        this.val = undefined;
        this.initList.initialized = false;
      }
    },
    selected(val) {
      this.$emit("selected", val);
    }
  },
  mounted() {
    if (this.params.options) {
      this.list.data = [...this.params.options];
    }
  },
  methods: {
    fetchList(params) {
      this.fetching = true;
      this.initList.initialized = true;
      this.fetchList.params = params;

      queryList(params)
        .then(res => {
          if (params === this.fetchList.params) {
            if (res.current === 1) {
              this.list = {
                ...res,
                pages: _.ceil(res.total / res.pageSize)
              };
            } else {
              this.list = {
                ...this.list,
                current: res.current,
                data: [...this.list.data, ...res.data]
              };
            }
          }
        })
        .finally(() => {
          if (params === this.fetchList.params) {
            this.fetching = false;
          }
        });
    },
    handleParams(searchValue) {
      const params = {
        current: 1,
        table: {
          name: this.column.table.name,
          no_default_field: true,
          fields: ["id", this.column.name],
          order_by: [[this.column.name, "ASC"]],
          filters: [[["state", "=", 1]]]
        }
      };

      if (searchValue) {
        params.table.filters[0].push([
          this.column.name,
          "LIKE",
          `%${searchValue}%`
        ]);
      }

      if (this.column.table.name === "role") {
        params.table.filters[0].push(["id", ">", 2]);
      }

      return params;
    },
    handleSearch(value) {
      this.fetchList(this.handleParams(value));
    },
    initList() {
      if (!this.val && !this.initList.initialized && !this.params.disabled) {
        this.list = { data: [] };
        this.initList.initialized = true;
        this.fetchList(this.handleParams());
      }
    },
    popupScroll(e) {
      if (this.fetching) return;

      const { scrollTop, clientHeight, scrollHeight } = e.target;

      if (
        scrollTop + clientHeight + 1 > scrollHeight &&
        this.list.pages > this.list.current
      ) {
        this.fetchList({
          ...this.fetchList.params,
          current: this.list.current + 1
        });
      }
    }
  }
};
</script>
