<template>
  <a-row type="flex" align="middle" style="flex-wrap: nowrap;">
    <Avatar :value="detail.avatar" :params="params" />
    <span v-if="detail.id" style="margin-left:8px">
      {{ detail.name || `uid(${detail.id})` }}
    </span>
  </a-row>
</template>

<script>
import { queryUserDetail } from "@/api/account";
import Avatar from "./Avatar.vue";

export default {
  name: "data-show-user",
  components: {
    Avatar
  },
  props: {
    value: Number,
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      detail: {}
    };
  },
  created() {
    this.value &&
      queryUserDetail({ id: this.value }).then(detail => {
        this.detail = detail;
      });
  }
};
</script>
