<template>
  <a-radio-group
    v-model="val"
    :options="options"
    :auto-focus="params.autoFocus"
    :disabled="params.disabled"
  />
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { queryAll } from "@/api";

export default {
  name: "data-entry-radio",
  props: {
    value: [Number, String],
    params: Object
  },
  data() {
    return {
      options: [],
      val: this.value
    };
  },
  computed: {
    ...mapState({
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id")
    }),
    column() {
      return this.allTableColumn[this.params.table_column_id];
    },
    selected() {
      const option = _.find(this.options, { value: this.val });
      return option && { id: option.value, [this.column.name]: option.label };
    }
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.val = newVal;
      }
    },
    val(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit("input", newVal);
      }
    }
  },
  mounted() {
    this.getOptions();
  },
  methods: {
    getOptions() {
      if (_.isEmpty(this.column)) return;

      queryAll({
        table: {
          name: this.column.table.name,
          no_default_field: true,
          fields: ["id", this.params.table_column_id]
        }
      }).then(({ data }) => {
        this.options = _.map(data, item => ({
          label: item[this.column.name],
          value: item.id
        }));
      });
    }
  }
};
</script>
