<template>
  <a-upload
    :action="action"
    :multiple="multiple"
    :show-upload-list="showUploadList"
    :before-upload="beforeUpload"
    :data="handleData"
    @change="handleChange"
    accept="image/jpeg, image/jpg, image/png, image/webp"
    class="image-upload"
  />
</template>

<script>
import { origins } from "@/dict";
import { fileH } from "@/utils";
import { handleImage } from "./utils/upload";

export default {
  name: "data-entry-uploadImage",
  props: {
    value: String,
    width: Number,
    height: Number,
    showUploadList: Boolean,
    multiple: Boolean
  },
  data() {
    return {
      action: `${origins.OSS}/upload`
    };
  },
  methods: {
    click(callback) {
      this.click.callback = callback;
      this.$el.getElementsByTagName("input")[0].click();
    },
    beforeUpload(file) {
      if (
        ["image/jpeg", "image/jpg", "image/png", "image/webp"].indexOf(
          file.type
        ) === -1
      ) {
        this.$message.error("只能上传jpg、jpg、png、webp格式图片");
        return false;
      }

      return handleImage({ file }).then(file => {
        if (file.status === "done") {
          this.click.callback && this.click.callback(file);
          return Promise.reject();
        }
        return file;
      });
    },
    handleData(file) {
      return {
        h: file.hash,
        s: fileH(file.hash).toString()
      };
    },
    handleChange({ file }) {
      file.status === "done" &&
        this.click.callback &&
        this.click.callback(file);
    }
  }
};
</script>
