import _ from "lodash";
import moment from "moment";
import Vue from "vue";
import { origins } from "@/dict";

_.forEach(
  {
    unixFormat: (v, f = "LLL") => (v > 0 ? moment.unix(v).format(f) : "-"),
    accountF: path => origins.ACCOUNT_FRONTEND + path,
    file: f => `${origins.FILE}/${f.hash}?name=${encodeURIComponent(f.name)}`,
    oss: hash => `${origins.OSS}/${hash.replace(/^(.{2})(.{2})/, "$1/$2/")}`,
    cdn: hash => `${origins.CDN}/${hash.replace(/^(.{2})(.{2})/, "$1/$2/")}`
  },
  (filter, key) => {
    Vue.filter(key, (data, ...agm) => filter(data, ...agm));
  }
);
