import _ from "lodash";
import { Modal } from "ant-design-vue";
import { SID, origins } from "@/dict";
import { login } from "@/api";
import {
  setToken,
  delToken,
  delAccountToken,
  getLocalStorageItem
} from "@/utils";

const getRedirect = () =>
  encodeURIComponent(window.location.href.replace(/(\?|&)auth=.+$/, ""));

export const goLogin = () => {
  window.location.replace(
    `${origins.ACCOUNT_FRONTEND}/login?redirect=${getRedirect()}`
  );
};

export const logout = () => {
  delAccountToken();
  delToken(SID);
  window.location.replace(
    `${origins.ACCOUNT_FRONTEND}/logout?redirect=${getRedirect()}`
  );
};

const loadingElement = document.getElementById("loading");
const showLoading = show => {
  _.set(loadingElement, "style.display", show !== false ? "flex" : "none");
};
showLoading(false);

export const handleLogin = (auth, next) => {
  showLoading();

  const tryToLoginNum = (localStorage.getItem("try_to_login_num") * 1 || 0) + 1;

  if (tryToLoginNum > 2) {
    localStorage.setItem("try_to_login_num", 0);
    logout();
    return;
  }

  localStorage.setItem("try_to_login_num", tryToLoginNum);

  login({ auth, sid: SID })
    .then(res => {
      if (!setToken(SID, res)) {
        Modal.error({
          title: "error",
          content: "Token error"
        });
      } else {
        next();
      }
    })
    .catch(error => {
      const languageBase = getLocalStorageItem("language_base_" + SID) || {};
      let errorCode = _.get(error, "response.data") || "";
      errorCode = errorCode.replace(/\n|\r$/, "");
      Modal.error({
        title: _.get(error, "response.status") || error.message,
        content: languageBase[errorCode] || errorCode,
        onOk() {
          delToken(SID);
          window.location.replace(origins.ACCOUNT_FRONTEND);
        }
      });
    })
    .finally(() => {
      showLoading(false);
    });
};
