import _ from "lodash";
import Vue from "vue";
import VueRouter from "vue-router";
import { SID, origins } from "@/dict";
import {
  delToken,
  getTokenUid,
  getAccountToken,
  setAccountToken,
  getTokenPayload
} from "@/utils";
import { goLogin, handleLogin } from "./func";
import Main from "@/components/Main.vue";
import Page404 from "@/views/404.vue";
import List from "@/views/List.vue";

!SID && window.location.replace(origins.ACCOUNT_FRONTEND);

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  base: `/${SID}`,
  routes: [
    {
      path: "/",
      name: "Main",
      component: Main,
      children: [
        {
          path: "/:tableName/list",
          name: "list",
          component: List
        }
      ]
    },
    {
      name: "404",
      path: "*",
      component: Page404
    }
  ]
});

router.beforeEach((to, from, next) => {
  // 有 uid 登录有效
  const uid = getTokenUid(SID);

  if (to.query.auth) {
    if (to.query.auth !== getAccountToken()) {
      // 新 token ，初始化尝试登录次数
      localStorage.setItem("try_to_login_num", 0);
    }

    // 保存 auth 。 auth 有效 并且 有uid，对比是不是同一个用户，不是则删除登录token
    if (setAccountToken(to.query.auth) && uid) {
      const { id } = getTokenPayload(to.query.auth);
      if (id && uid !== id) {
        delToken(SID);
      }
    }

    // 删除auth
    router.replace(
      _.pick(
        {
          ...to,
          query: _.omit(to.query, ["auth"])
        },
        ["name", "params", "query"]
      )
    );
  } else if (!uid) {
    const auth = getAccountToken();
    !auth ? goLogin() : handleLogin(auth, next);
  } else {
    next();
  }
});

export default router;
