import _ from "lodash";
import { storeOptions } from "./default";
import { queryAll } from "@/api";

const tableName = "language_table";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["table", "native_name"]
      }
    }
  }
});

options.state = {
  ...options.state,
  values: {}
};

options.mutations = {
  ...options.mutations,
  setValues(state, values) {
    state.values = values;
  }
};

options.actions = {
  ...options.actions,
  get({ commit, dispatch }, localeId) {
    dispatch("getAll", {
      table: {
        filters: [
          [
            ["locale", "=", localeId],
            ["table", ">", 8]
          ]
        ]
      }
    }).then(({ data }) => {
      const values = {};
      _.forEach(data, item => {
        values[item.table.name] = item.native_name;
      });
      commit("setValues", values);
    });
  }
};

export default {
  namespaced: true,
  ...options
};
