import _ from "lodash";
import { accountRequest } from "./request";
import query from "./query";

export const queryAll = data => query(accountRequest, "all", data);
export const queryList = data => query(accountRequest, "list", data);

export const queryUserDetail = ({ id }) =>
  new Promise((resolve, reject) => {
    if (!id) return resolve();

    const resFun = () => resolve(queryUserDetail.users[id]);

    if (queryUserDetail.users[id]) return resFun();

    clearTimeout(queryUserDetail.timeout);

    queryUserDetail.ids = _.uniq([...queryUserDetail.ids, id]);
    queryUserDetail.resolves = [...queryUserDetail.resolves, resFun];
    queryUserDetail.rejects = [...queryUserDetail.rejects, reject];

    queryUserDetail.timeout = setTimeout(() => {
      const { ids, resolves, rejects } = queryUserDetail;
      queryUserDetail.ids = [];
      queryUserDetail.resolves = [];
      queryUserDetail.rejects = [];

      queryAll({
        table: {
          name: "user",
          no_default_field: true,
          fields: ["id", "avatar", "name"],
          filters: [[["id", "IN", ids]]]
        }
      })
        .then(({ data }) => {
          _.forEach(data, user => {
            queryUserDetail.users[user.id] = user;
          });

          _.forEach(resolves, resFun => resFun());
        })
        .catch(err => {
          _.forEach(rejects, reject => reject(err));
        });
    }, 500);
  });

queryUserDetail.ids = [];
queryUserDetail.users = {};
queryUserDetail.resolves = [];
queryUserDetail.rejects = [];
