<template>
  <a-drawer
    :title="title"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    @close="close"
    :headerStyle="{ textAlign: 'center' }"
  >
    <a-spin v-show="loading" />
    <a-form-model v-if="!loading" @submit="handleSubmit" @submit.native.prevent>
      <a-form-model-item>
        <PermissionTree
          ref="permissionTree"
          :permission="rolePermission"
          :difference-permission="memberPermission"
        />
      </a-form-model-item>
      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { queryAll, save } from "@/api";
import { queryUserDetail } from "@/api/account";
import PermissionTree from "./PermissionTree.vue";

export default {
  components: {
    PermissionTree
  },
  data() {
    return {
      visible: false,
      title: "",
      width: document.body.offsetWidth < 450 ? document.body.offsetWidth : 450,
      submitLoding: false,
      detail: {},
      memberPermission: "",
      rolePermission: "",
      loading: true
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  methods: {
    show(member) {
      this.detail = member;
      queryUserDetail({ id: member.uid }).then((user = {}) => {
        this.title = `${this.languageTable.member} - ${user.name ||
          "uid(" + member.uid + ")"} - ${this.languageBase.permission}`;
      });

      this.loading = true;
      this.visible = true;

      queryAll({
        table: {
          name: "member",
          no_default_field: true,
          fields: ["permission"],
          filters: [[["id", "=", member.id]]]
        },
        left_join_tables: [
          {
            name: "role",
            no_default_field: true,
            fields: ["permission"],
            on: ["id", "member", "role"],
            ons: [["state", "=", 1]]
          }
        ]
      }).then(({ data }) => {
        this.memberPermission = _.get(data, "[0].permission");
        this.rolePermission = _.get(data, "[0].role.permission");
        this.loading = false;
      });
    },
    close() {
      this.visible = false;
    },
    handleSubmit() {
      const {
        checkedKeys,
        oldCheckedKeys,
        roleCheckedKeys,
        checkedKeysToPermission
      } = this.$refs.permissionTree;

      if (
        _.isEmpty(
          _.omitBy(_.xor(checkedKeys, oldCheckedKeys), key =>
            /\d+-(detail|update|list|search)$/.test(key)
          )
        )
      ) {
        this.close();
        return;
      }

      const disabledKeys = _.difference(roleCheckedKeys, checkedKeys);
      const enabledKeys = _.difference(checkedKeys, roleCheckedKeys);

      const permission = _.omitBy(
        {
          disabled: checkedKeysToPermission(disabledKeys),
          enabled: checkedKeysToPermission(enabledKeys)
        },
        _.isEmpty
      );

      save({
        table: { name: "member" },
        rows: [
          {
            id: this.detail.id,
            permission: _.isEmpty(permission)
              ? null
              : JSON.stringify(permission)
          }
        ]
      }).then(() => {
        this.$notification.success({
          message: this.languageBase.save,
          comment: this.languageBase.successful
        });
        this.close();
      });
    }
  }
};
</script>
