import _ from "lodash";
import { h } from "@/utils";
import { systemRequest } from "./request";
import query from "./query";

/**
 * 登录
 * @param {Object} param - { auth: accountToken, sid: systemId }
 * @param {string} param.auth
 * @param {string|Number} param.sid
 * @returns {Promise}
 */
export const login = ({ auth, sid }) =>
  systemRequest.post("/login", { h: h(auth, sid), sid }, { headers: { auth } });

/**
 * 查询数据是否存在
 * @param {Object} data - data = { table_name: tableName, filters: [[[field, operator, value], ...(AND)], ...(OR)]}
 * @param {string} data.table_name
 * @param {Array} data.filters
 * @returns {Promise}
 */
export const exist = data => systemRequest.post("/exist", data);

/**
 * 读取表的全部数据
 * @param {Object} data - 查看 query 方法的参数 data
 */
export const queryAll = data => query(systemRequest, "all", data);

/**
 * 分页读取表的数据
 * @param {Object} data - 查看 query 方法的参数 data
 */
export const queryList = data => query(systemRequest, "list", data);

/**
 * 分页读取表的数据
 * @param {Object} data - { table: { id, name }, rows: [{ [field]: value }, ...] }
 * @param {Object} data.table - id、name 其中一个必需有值
 * @param {number} data.table.id
 * @param {string} data.table.name
 * @param {{[fieldName]:any}[]} data.rows
 */
export const save = data =>
  systemRequest.post("/save", data).then(res => {
    _.forOwn(sessionStorage, (value, key) => {
      const RE = new RegExp(`^q-${data.table.name}-`);
      RE.test(key) && sessionStorage.removeItem(key);
    });
    return res;
  });
