<template>
  <a-upload
    multiple
    :action="action"
    :before-upload="beforeUpload"
    :data="handleData"
    :file-list="fileList"
    @change="handleChange"
  >
    <a-button icon="upload">上传</a-button>
  </a-upload>
</template>

<script>
import _ from "lodash";
import { origins } from "@/dict";
import { handleFile } from "./utils/upload";
import { fileH } from "@/utils";

export default {
  name: "data-entry-upload",
  props: {
    value: String
  },
  data() {
    let fileList = [];
    try {
      fileList = _.map(JSON.parse(this.value), file => ({
        ...file,
        url: this.getUrl(file),
        uid: file.hash,
        status: "done"
      }));
    } catch (err) {
      // no todo
    }

    return {
      action: `${origins.OSS}/upload`,
      fileList
    };
  },
  watch: {
    fileList(val) {
      val = _.uniqBy(val, "hash");
      val = _.map(val, item => _.pick(item, ["name", "hash"]));
      this.$emit("input", JSON.stringify(val));
    }
  },
  methods: {
    beforeUpload(file) {
      return handleFile(file).then(file => {
        if (file.status) {
          if (_.findIndex(this.fileList, { hash: file.hash }) === -1) {
            file.url = this.getUrl(file);
            this.fileList = [...this.fileList, file];
          } else {
            this.$message.info({ content: `${file.name} 已选择` });
          }
          return Promise.reject();
        }
        return file;
      });
    },
    handleData(file) {
      return { h: file.hash, s: fileH(file.hash).toString(), private: "1" };
    },
    handleChange({ fileList }) {
      fileList = _.map(fileList, file => {
        if (file.response && !file.url) {
          file.url = this.getUrl(file);
        }
        return file;
      });
      this.fileList = _.uniqBy(fileList, "hash");
    },
    getUrl(file) {
      return this.$options.filters.file(file);
    }
  }
};
</script>
