<template>
  <a-upload
    :action="action"
    :show-upload-list="false"
    :before-upload="beforeUpload"
    :data="handleData"
    @change="handleChange"
    accept="image/jpeg, image/jpg, image/png, image/webp"
  >
    <a-avatar v-show="loading" :size="100" icon="loading" />
    <template v-if="!loading">
      <a-avatar v-if="src" :size="100" :src="src" />
      <a-avatar v-else-if="value" :size="100" :src="value | cdn" />
      <a-avatar v-else :size="100" icon="user" />
    </template>
  </a-upload>
</template>

<script>
import { fileH, fileToUrl } from "@/utils";
import { origins } from "@/dict";
import { handleImage } from "./utils/upload";

export default {
  name: "data-entry-avatar",
  props: {
    value: String
  },
  data() {
    return {
      action: `${origins.OSS}/upload`,
      loading: false,
      src: ""
    };
  },
  methods: {
    beforeUpload(file) {
      if (
        ["image/jpeg", "image/jpg", "image/png", "image/webp"].indexOf(
          file.type
        ) === -1
      ) {
        this.$message.error("只能上传jpg、jpg、png、webp格式图片");
        return false;
      }

      return new Promise((resolve, reject) => {
        handleImage({ file, width: 100, height: 100 })
          .then(file => {
            fileToUrl(file)
              .then(src => {
                this.src = src;

                if (file.status === "done") {
                  this.$emit("input", file.hash);
                  return reject();
                }

                resolve(file);
              })
              .catch(reject);
          })
          .catch(reject);
      });
    },
    handleData(file) {
      return {
        h: file.hash,
        s: fileH(file.hash).toString()
      };
    },
    handleChange({ file }) {
      if (file.status === "done") {
        this.$emit("input", file.hash);
      }
      this.loading = file.status === "uploading";
    }
  }
};
</script>
