<template>
  <a-drawer
    :title="isEdit ? languageBase.edit : languageBase.create"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    :headerStyle="{ textAlign: 'center' }"
    @close="close"
  >
    <template v-if="isEdit">
      <p v-if="!permission.update">没有权限</p>
      <p v-else-if="!loading && !detail.data.id">
        {{ languageBase.record_not_exist }}
      </p>
    </template>

    <a-spin v-show="loading && !submitLoding" />

    <a-form-model
      v-if="visibleForm"
      ref="ruleForm"
      :model="formData"
      :rules="rules"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <template v-for="(group, index) in tableColumnGroup">
        <a-row :key="index" type="flex" justify="space-between">
          <template v-for="column in group.columns">
            <a-col
              v-if="showItem(column.name)"
              :key="column.id"
              :span="group.span"
            >
              <a-form-model-item
                v-if="disabledItem(column.name)"
                :label="getLabel(column.name)"
              >
                <DataShow
                  :value="detail.data[column.name]"
                  :component-name="column.data_type.component"
                  :params="column.data_type.params"
                />
              </a-form-model-item>
              <a-form-model-item
                v-else
                :prop="column.name"
                :label="getLabel(column.name)"
              >
                <DataEntry
                  :ref="`data-entry-${column.name}`"
                  v-model="formData[column.name]"
                  :component-name="column.data_type.component"
                  :params="{
                    ...formDataDataType[column.name].params,
                    autoFocus: autoFocus()
                  }"
                />
              </a-form-model-item>
            </a-col>
          </template>
        </a-row>
      </template>

      <template v-if="isEdit">
        <a-row type="flex" justify="space-between">
          <a-col v-if="showItem('id')" :span="11">
            <a-form-model-item label="id">
              {{ detail.data.id }}
            </a-form-model-item>
          </a-col>
          <a-col v-if="showItem('state')" :span="11">
            <a-form-model-item :label="languageBase.state">
              <DataEntry
                v-model="formData.state"
                component-name="switch"
                :params="{
                  checkedChildren: languageBase.enable,
                  unCheckedChildren: languageBase.disable,
                  disabled: disabledItem('state')
                }"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row type="flex" justify="space-between">
          <a-col v-if="showItem('created_at')" :span="11">
            <a-form-model-item :label="languageBase.created_at">
              {{ detail.data.created_at | unixFormat }}
            </a-form-model-item>
          </a-col>
          <a-col v-if="showItem('updated_at')" :span="11">
            <a-form-model-item :label="languageBase.updated_at">
              {{ detail.data.updated_at | unixFormat }}
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row type="flex" justify="space-between">
          <a-col v-if="showItem('created_by_uid')" :span="11">
            <a-form-model-item :label="languageBase.created_by_uid">
              <DataShow
                component-name="user"
                :value="detail.data.created_by_uid"
                :params="{ size: 'small' }"
              />
            </a-form-model-item>
          </a-col>
          <a-col v-if="showItem('updated_by_uid')" :span="11">
            <a-form-model-item :label="languageBase.updated_by_uid">
              <DataShow
                component-name="user"
                :value="detail.data.updated_by_uid"
                :params="{ size: 'small' }"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </template>
      <template v-else>
        <a-form-model-item :label="languageBase.state">
          <DataEntry
            v-model="formData.state"
            component-name="switch"
            :params="{
              checkedChildren: languageBase.enable,
              unCheckedChildren: languageBase.disable
            }"
          />
        </a-form-model-item>
      </template>

      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState, mapMutations, mapActions } from "vuex";
import { getAccountTokenId } from "@/utils";
import DataEntry from "./dataEntry/index.vue";
import DataShow from "./dataShow/index.vue";

export default {
  props: {
    visible: Boolean,
    width: {
      type: [String, Number],
      default: document.body.offsetWidth < 768 ? document.body.offsetWidth : 768
    },
    tableName: String,
    permission: Object,
    id: Number
  },
  components: {
    DataEntry,
    DataShow
  },
  data() {
    return {
      uid: getAccountTokenId(),
      submitLoding: false,
      formData: {},
      oldFormData: {},
      formDataDataType: {},
      inputRequired: {
        required: true,
        validator: (rule, value, callback) => {
          value === "" ||
          value === undefined ||
          value === null ||
          _.isNaN(value)
            ? callback(this.languageBase.please_input)
            : callback();
        }
      },
      // 列在 languageBase 的配置
      LBTC_RE: /^(name|comment)$/
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id"),
      dataTypes: store => _.keyBy(store.data_type.all.data, "id"),
      detail: store => store.list.detail,
      loading: store =>
        store.languageBase.all.loading ||
        store.languageTableColumn.all.loading ||
        store.table_column.all.loading ||
        store.data_type.all.loading ||
        store.list.detail.loading
    }),
    isEdit() {
      return this.id > 0;
    },
    // 初始化情况字段
    detailFields() {
      if (_.isEmpty(this.permission) || _.isEmpty(this.allTableColumn))
        return [];

      const fields = [];

      _.forEach(_.get(this.permission, "detail.fields"), id => {
        const key = _.get(this.allTableColumn, `${id}.name`);
        // 有才加。可能存在权限设置后，字段被删
        key && fields.push(key);
      });

      _.forEach(_.get(this.permission, "detail.defaultFields"), field => {
        fields.push(field);
      });
      return fields;
    },
    // 当前表的全部列的信息
    tableColumns() {
      if (_.isEmpty(this.allTableColumn) || _.isEmpty(this.dataTypes))
        return [];

      // 获取当前表的全部列
      let tableColumns = _.filter(
        this.allTableColumn,
        item =>
          item.table.name === this.tableName &&
          (!/^(role|member)$/.test(this.tableName) ||
            item.name !== "permission")
      );
      tableColumns = _.map(tableColumns, item => ({
        ...item,
        // 数据类型的信息
        data_type: {
          ...item.data_type,
          ..._.pick(this.dataTypes[item.data_type.id], ["component", "params"])
        }
      }));

      // 排序并返回
      return _.orderBy(tableColumns, ["index"]);
    },
    // 当前表的全部列的信息，以列名为 key 的对象
    tableColumnFromName() {
      return _.keyBy(this.tableColumns, "name");
    },
    // 当前表的全部列分组信息
    tableColumnGroup() {
      if (_.isEmpty(this.tableColumns)) return [];

      // index 值为 Number 或 null ，整数相同为一组
      const tableColumnGroup = _.groupBy(this.tableColumns, item =>
        item.index ? _.floor(item.index) : item.index
      );

      return [
        // 非 null 组，同组等分同一行宽度
        ..._.map(
          _.pickBy(tableColumnGroup, (v, k) => k !== "null"),
          item => ({
            columns: item,
            span: item.length > 1 ? _.floor(24 / item.length) - 1 : 24
          })
        ),
        // null 组的每项独占一行
        ..._.map(tableColumnGroup.null, item => ({
          columns: [item],
          span: 24
        }))
      ];
    },
    // 规则
    rules() {
      if (_.isEmpty(this.tableColumns)) return {};

      const rules = {};
      const lengthTemplate = _.template(this.languageBase.character_template);

      _.forEach(this.tableColumns, column => {
        // 非 Boolean 类型才验证
        if (/^(switch)$/.test(column.data_type.component)) return;

        rules[column.name] = [];
        !column.null && rules[column.name].push(this.inputRequired);

        if (/^(upload|images)$/.test(column.data_type.component)) return;

        let type = "string";
        let message = lengthTemplate(column);
        if (
          /^(radio|radioSelect|inputNumber|rate|datePicker|cascader)$/.test(
            column.data_type.component
          )
        ) {
          type = "number";
          message = `${column.min} ~ ${column.max}`;
        } else if (
          /^(multipleSelect|checkbox)$/.test(column.data_type.component)
        ) {
          type = "array";
          message = `${column.min} ~ ${column.max}`;
        }

        rules[column.name].push({
          type,
          min: column.min,
          max: column.max,
          message,
          trigger: "blur"
        });
      });
      return rules;
    },
    // 新增数据的默认表单值
    createFormData() {
      if (_.isEmpty(this.tableColumns)) return {};

      const formData = { state: false };

      _.forEach(this.tableColumns, column => {
        let value = column.default || undefined;
        if (
          /^(inputNumber|radio|radioSelect|rate)$/.test(
            column.data_type.component
          ) &&
          value
        ) {
          // 值为 Number
          value *= 1;
        } else if (column.data_type.component === "switch") {
          // 值为 Boolean
          value = column.default * 1 === 1;
        }
        // 其它类型未写完
        formData[column.name] = value;
      });
      return formData;
    },
    // 可更新的字段
    editFormDataKeys() {
      if (
        _.isEmpty(this.permission) ||
        _.isEmpty(this.allTableColumn) ||
        _.isEmpty(this.detailFields)
      )
        return [];

      const keys = [...(_.get(this.permission, "update.defaultFields") || [])];

      _.forEach(_.get(this.permission, "update.fields"), id => {
        const key = _.get(this.allTableColumn, `${id}.name`);
        //
        key && _.indexOf(this.detailFields, key) !== -1 && keys.push(key);
      });
      return keys;
    },
    visibleForm() {
      return (
        this.visible &&
        !_.isEmpty(this.formData) &&
        (this.submitLoding || !this.loading)
      );
    }
  },
  watch: {
    visible() {
      this.initFormData();
    },
    createFormData(newVal, oldVal) {
      !this.isEdit && !_.isEqual(newVal, oldVal) && this.initFormData();
    },
    editFormDataKeys(newVal, oldVal) {
      this.isEdit && !_.isEqual(newVal, oldVal) && this.initFormData();
    },
    tableColumnFromName(newVal, oldVal) {
      !_.isEqual(newVal, oldVal) && this.initFormData();
    }
  },
  mounted() {
    this.initFormData();
  },
  methods: {
    ...mapMutations("list", {
      setItem: "setItem"
    }),
    ...mapActions("list", {
      getDetail: "getDetail",
      save: "save"
    }),
    initFormData() {
      if (
        !this.visible ||
        (this.isEdit && _.isEmpty(this.editFormDataKeys)) ||
        (!this.isEdit && _.isEmpty(this.createFormData)) ||
        _.isEmpty(this.tableColumnFromName)
      )
        return;

      this.autoFocus.have = false;

      if (this.isEdit) {
        this.formData = {};

        this.getDetail({
          table: {
            name: this.tableName,
            no_default_field: true,
            fields: this.detailFields,
            filters: [[["id", "=", this.id]]]
          }
        }).then(() => {
          if (_.isEmpty(this.detail.data)) return;

          this.oldFormData = {};
          _.forEach(this.editFormDataKeys, key => {
            const dataType = this.getDataType(key);
            const component = dataType.component;

            if (key === "state" || component === "switch") {
              this.oldFormData[key] = !!this.detail.data[key];
            } else if (component === "radioSelect") {
              this.oldFormData[key] = this.detail.data[key].id || undefined;
              if (this.oldFormData[key]) {
                dataType.params.options = [this.detail.data[key]];
              }
            } else if (component === "radio") {
              this.oldFormData[key] = this.detail.data[key].id || undefined;
            } else if (/^(multipleSelect|checkbox)$/.test(component)) {
              this.oldFormData[key] =
                _.map(this.detail.data[key], item => item.id) || undefined;
              if (this.oldFormData[key]) {
                dataType.params.options = this.detail.data[key];
              }
            } else if (component === "cascader") {
              this.oldFormData[key] = this.detail.data[key];
              const column = this.allTableColumn[
                dataType.params.table_column_id
              ];
              dataType.params.tableName = this.tableName;
              dataType.params.id = this.id;
              dataType.params.options = _.get(
                this.detail.extraData,
                `cascader_${column.table.name}`
              );
            } else {
              this.oldFormData[key] = this.detail.data[key];
            }

            this.formDataDataType[key] = dataType;
          });
          this.formData = { ...this.oldFormData };
        });
      } else {
        this.formData = { ...this.createFormData };
        _.forEach(this.formData, (v, k) => {
          this.formDataDataType[k] = this.getDataType(k);
        });
      }
    },
    close() {
      !this.detail.data.id
        ? this.$emit("close")
        : this.$confirm({
            title: "您确定要关闭吗？",
            content: "编辑内容未保存",
            onOk: () => {
              this.$emit("close");
            }
          });
    },
    handleSubmit() {
      if (this.submitLoding) return;

      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;

        let { formData } = this;

        if (this.id) {
          formData = _.pickBy(
            formData,
            (value, key) => value !== this.oldFormData[key]
          );

          if (_.isEmpty(formData)) {
            this.$emit("close");
            return;
          }

          formData.id = this.id;
        }

        // 空值处理
        formData.id &&
          _.forEach(
            _.filter(
              this.tableColumns,
              column =>
                _.has(formData, column.name) &&
                formData[column.name] === undefined
            ),
            column => {
              if (column.null) {
                formData[column.name] = null;
              }
            }
          );

        this.submitLoding = true;
        this.save({
          tableName: this.tableName,
          record: formData
        })
          .then(() => {
            this.$notification.success({
              message: this.languageBase.save,
              comment: this.languageBase.successful
            });
            if (formData.id) {
              const record = {};
              _.forEach(formData, (v, k) => {
                if (_.isObject(this.detail.data[k]) || _.isArray(v)) {
                  record[k] = this.$refs[
                    `data-entry-${k}`
                  ][0].$children[0].selected;
                }
              });
              if (!_.isEmpty(record)) {
                record.id = formData.id;
                this.setItem(record);
              }
            }
            this.$emit("close");
          })
          .finally(() => {
            this.submitLoding = false;
          });
      });
    },
    getDataType(key) {
      const dataType =
        _.get(this.tableColumnFromName, `${key}.data_type`) || {};

      return {
        ...dataType,
        params: { ...dataType.params }
      };
    },
    showItem(key) {
      return !this.isEdit || _.has(this.detail.data, key);
    },
    disabledItem(key) {
      if (!this.isEdit) return false;

      if (
        key === "state" &&
        this.tableName === "member" &&
        (this.detail.data.id === 1 || this.detail.data.uid === this.uid)
      )
        return true;

      return _.indexOf(this.editFormDataKeys, key) === -1;
    },
    autoFocus() {
      if (this.autoFocus.have) return false;
      this.autoFocus.have = true;
      return this.autoFocus.have;
    },
    getLabel(name) {
      let label = this.languageTableColumn[`${this.tableName}.${name}`];
      return (
        label || (this.LBTC_RE.test(name) && this.languageBase[name]) || name
      );
    }
  }
};
</script>
