<template>
  <a-date-picker v-model="val" :format="params.format" :showTime="showTime" />
</template>

<script>
import moment from "moment";

export default {
  name: "data-entry-datePicker",
  props: {
    value: Number,
    params: Object
  },
  data() {
    return {
      val: this.toMoment(this.value)
    };
  },
  computed: {
    showTime() {
      return /lll|LLL|H|h|m|s/.test(this.params.format);
    }
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.val = this.toMoment(newVal);
      }
    },
    val(newVal, oldVal) {
      newVal = this.toUnix(newVal);
      oldVal = this.toUnix(oldVal);
      if (newVal !== oldVal) {
        this.$emit("input", newVal);
      }
    }
  },
  methods: {
    toMoment(u) {
      return u > 0 ? moment.unix(u) : undefined;
    },
    toUnix(m) {
      return m ? m.unix() : undefined;
    }
  }
};
</script>
