import { crypto } from "./crypto";

/**
 * 计算文件 MD5 值。
 * 只适合小文件，大文件最好分片(blobSlice)读取
 * @param {Blob} file
 * @returns {Promise} .then(hash => {})
 */
export const md5File = file =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsArrayBuffer(file);
    reader.onload = () =>
      resolve(
        crypto.MD5(crypto.lib.WordArray.create(reader.result)).toString()
      );
    reader.onerror = e => reject(e);
  });

/**
 * 文件hash加密。
 * 奇怪的命名，有意的
 * @param {string} s fileHash
 */
export const fileH = s =>
  crypto.MD5("tn0Zc7YrEFmLp56o" + s + "0n9M1KrAQ84t6lsH").toString();

// 用于登录
let s = "";
const m = s => crypto.MD5(s).toString();
export const slk = key => {
  s = key;
};
export const h = (a, b) => m(a + s + b);
