import _ from "lodash";
import { getSessionStorageItem, setSessionStorageItem, crypto } from "@/utils";
import { SID } from "@/dict";

/**
 * 生成 sessionStorage key
 * @param {Object} params
 * @returns {string}
 */
const createSSKey = params => {
  delete params.sync;
  return `q-${params.table.name}-${crypto
    .MD5(JSON.stringify({ ...params, SID }))
    .toString()}`;
};

/**
 * 访问缓存, { [key]: request } 。
 * 当缓存存在时，可直接读取，防止同时相同连接多次访问接口。
 * 读取完成后，需要删除: delete requestCache[key];
 */
const requestCache = {};

/**
 * 查询接口
 * @param {Object} request - Axios
 * @param {string} type - 查询的类型："all" | "list"，必需
 * @param {Object} data - 查询参数，必需
 * @param {number} data.current - 当前页数, type 为 ”list“ 有效。值为1时接口才会返回总数量，否则不返回
 * @param {number} data.pageSize - 每页数量, type 为 ”list“ 有效
 * @param {Object} data.table - id、name、column_id 其中一个必需有值
 * @param {number} data.table.id - 表id，大于0
 * @param {string} data.table.name - 表名
 * @param {string} data.table.as_name - 表的别名
 * @param {number} data.table.column_id - 表任一列id
 * @param {boolean} data.table.no_default_field - 不返回默认字段，默认字段：id、state、created_at、updated_at
 * @param {(string|number)[]} data.table.fields - 返回字段，[field1, field2, fieldId1, ...]
 * @param {[(string|number),string,any][][]} data.table.filters - 条件 [[[field1, "=", "0"], [field2, "!=", "0"], ...], [[field3, "=", "1"], ...]] => field1=0 and field2!=0 or field3=1
 * @param {[(string|number),string][]} data.table.order_by - 排序 [[field1, "ASC"], [field2, "DESC"]]
 * @param {Object[]} data.join_tables - 连表, [join_table], join_table 跟 table 同类
 * @param {Object[]} data.left_join_tables - 左连接, [left_join_table], left_join_table 跟 table 同类
 * @returns {Promise}
 */
export const query = (request, type, data) =>
  new Promise((resolve, reject) => {
    const sync = data.sync;
    const key = createSSKey(data);

    if (!sync) {
      const value = getSessionStorageItem(key);
      if (!_.isEmpty(value)) {
        return resolve(value);
      }
    }

    let req = requestCache[key];
    if (!req) {
      // 缓存
      requestCache[key] = request.post(`/query/${type}`, data).then(res => {
        setSessionStorageItem(key, res);
        return res;
      });
      req = requestCache[key];
    }

    req
      .then(resolve)
      .catch(reject)
      .finally(() => {
        // 删除缓存
        delete requestCache[key];
      });
  });

export default query;
