<template>
  <a-dropdown placement="bottomCenter">
    <a-button type="link" icon="global">
      {{ locale.selected.native_name || "language" }}
    </a-button>

    <a-menu slot="overlay" style="overflow-y: auto;max-height:80vh">
      <a-menu-item v-show="locale.all.loading">
        <a-spin />
      </a-menu-item>
      <a-menu-item v-for="(item, index) in locale.all.data" :key="index">
        <a @click.prevent="select(item)">{{ item.native_name }}</a>
      </a-menu-item>
    </a-menu>
  </a-dropdown>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  computed: {
    ...mapState({
      locale: store => store.locale
    })
  },
  watch: {
    "locale.selected"() {
      this.handleGetLanguage();
    }
  },
  created() {
    this.getLocale();
    this.handleGetLanguage();
  },
  methods: {
    ...mapMutations("locale", {
      select: "select"
    }),
    ...mapActions("locale", {
      getLocale: "getAll"
    }),
    ...mapActions("languageBase", {
      getLanguageBase: "get"
    }),
    ...mapActions("languageTable", {
      getLanguageTable: "get"
    }),
    ...mapActions("languageTableColumn", {
      getLanguageTableColumn: "get"
    }),
    ...mapActions("languageMenuGroup", {
      getLanguageMenuGroup: "get"
    }),
    handleGetLanguage() {
      const localeId = this.locale.selected.id ? this.locale.selected.id : 50;
      this.getLanguageBase(localeId);
      this.getLanguageTable(localeId);
      this.getLanguageTableColumn(localeId);
      this.getLanguageMenuGroup(localeId);
    }
  }
};
</script>
