<script>
import _ from "lodash";
import Avatar from "./Avatar";
import Image from "./Image";
import Rate from "./Rate";
import Switch from "./Switch";
import Upload from "./Upload";
import User from "./User";

const Components = {
  [Avatar.name]: Avatar,
  [Image.name]: Image,
  [Rate.name]: Rate,
  [Switch.name]: Switch,
  [Upload.name]: Upload,
  [User.name]: User
};

export default {
  props: {
    value: [String, Number, Boolean, Array, Object],
    componentName: String,
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      val: undefined
    };
  },
  watch: {
    value(newVal, oldVal) {
      !_.isEqual(newVal, oldVal) && this.formatVal();
    }
  },
  created() {
    this.formatVal();
  },
  render(h) {
    if (/^(avatar|user|switch|rate|upload|image)$/.test(this.componentName))
      return h(Components["data-show-" + this.componentName], {
        props: {
          value: this.val,
          params: this.params
        }
      });

    if (this.componentName === "richText")
      return h("div", { domProps: { innerHTML: this.val } });

    if (this.componentName === "textarea")
      return h("div", { style: { whiteSpace: "pre-wrap" } }, this.val);

    if (_.isArray(this.val)) {
      this.val = _.join(
        _.map(
          this.val,
          item => _.values(_.omit(item, ["elm", "id", "isRootInsert"]))[0]
        ),
        ", "
      );
    } else if (_.isObject(this.val)) {
      _.forOwn(this.val, (v, k) => {
        if (k !== "id") {
          this.val = v;
          return false;
        }
      });
    }

    return h("span", {}, this.val);
  },
  methods: {
    formatVal() {
      let val = this.value;
      if (
        /^(input|textarea|upload|avatar)$/.test(this.componentName) &&
        !_.isString(val)
      ) {
        val = "";
      } else if (
        /^(inputNumber|rate|year|monthPicker)$/.test(this.componentName) &&
        !_.isNumber(val)
      ) {
        val = undefined;
      } else if (
        /^(multipleSelect|checkbox)$/.test(this.componentName) &&
        !_.isArray(val)
      ) {
        val = [];
      } else if (this.componentName === "richText") {
        if (_.isString(val)) {
          val = val.replace(/<img data-hash="([a-z0-9]{32})">/g, (img, hash) =>
            img.replace(
              `data-hash="${hash}`,
              `src="${this.$options.filters.cdn(hash)}`
            )
          );
          val = `<div class="ql-snow"><div class="ql-editor">${val}</div></div>`;
        } else {
          val = "";
        }
      } else if (this.componentName === "datePicker") {
        val = this.$options.filters.unixFormat(val, this.params.format);
      } else if (this.componentName === "cascader" && val) {
        const { columnName, options } = this.params;
        const ids = [val];
        let option = _.find(options, { id: val });
        if (option) {
          val = option[columnName];
          while (option && option.pid && _.indexOf(ids, option.pid) === -1) {
            ids.push(option.pid);
            option = _.find(options, { id: option.pid });
            val = `${option[columnName]} / ${val}`;
          }
        }
      } else if (this.componentName === "switch") {
        val = !!val;
      }

      this.val = val;
    }
  }
};
</script>
