import _ from "lodash";
import axios from "axios";
import { notification, Modal } from "ant-design-vue";
import { SID, origins } from "@/dict";
import { getLocalStorageItem } from "./localStorage";
import { getToken, delToken } from "./token";

/**
 * 系统未启动时弹窗提示
 * @param {string} title - 弹窗标题
 */
const showSystemStopped = title => {
  delToken(SID);
  !showSystemStopped.showed &&
    Modal.error({
      title,
      onOk() {
        window.location.replace(origins.ACCOUNT_FRONTEND);
      }
    });
  showSystemStopped.showed = true;
};

/**
 * 创建 AxiosInstance
 * @param {Object} config - AxiosRequestConfig
 * @returns {Object} 返回 AxiosInstance
 */
export const createRequest = config => {
  const instance = axios.create(config);

  // 拦截请求
  instance.interceptors.request.use(
    config => {
      if (config.useAuth !== false) {
        config.headers.auth = config.headers.auth || getToken(SID);
      }
      return config;
    },
    error => {
      notification.error({
        message: error.message
      });
      return Promise.reject(error);
    }
  );

  // 拦截回应
  instance.interceptors.response.use(
    response => response.data,
    error => {
      if (_.get(error, "response.status") === 401 && getToken(SID)) {
        delToken(SID);
        window.location.reload();
      } else {
        const languageBase = getLocalStorageItem("language_base_" + SID) || {};

        if (error.config.showError !== false) {
          let errorCode = _.get(error, "response.data") || "";
          errorCode = errorCode.replace(/\n|\r$/, "");

          if (errorCode === "system_stopped") {
            showSystemStopped(languageBase.system_stopped || "System stopped");
          } else {
            notification.error({
              message: _.get(error, "response.status") || error.message,
              description: languageBase[errorCode] || errorCode
            });
          }
        }
      }

      return Promise.reject(error);
    }
  );

  return instance;
};
