import _ from "lodash";
import { Modal } from "ant-design-vue";
import { SID, origins } from "@/dict";
import { systemRequest } from "@/api";
import { getAccountTokenId, getLocalStorageItem } from "@/utils";
import PermissionTree from "@/components/PermissionTree.vue";

const tableName = "member";

const state = {
  loading: true,
  values: {}
};

const mutations = {
  setValues(state, values) {
    state.loading = false;
    state.values = values;
  }
};

const actions = {
  fetch({ commit }) {
    return systemRequest
      .post("/query/all", {
        table: {
          name: tableName,
          no_default_field: true,
          fields: ["permission", "state"],
          filters: [[["uid", "=", getAccountTokenId()]]]
        },
        left_join_tables: [
          {
            name: "role",
            no_default_field: true,
            fields: ["permission"],
            on: ["id", tableName, "role"],
            ons: [["state", "=", 1]]
          }
        ]
      })
      .then(({ data }) => {
        const member = data[0];
        if (member) {
          if (!member.state) {
            const languageBase =
              getLocalStorageItem("language_base_" + SID) || {};

            Modal.error({
              title: languageBase.account_disabled || "你的账号已禁用。",
              onOk() {
                window.location.replace(origins.ACCOUNT_FRONTEND);
              }
            });
          } else {
            const {
              permissionToCheckedKeys,
              checkedKeysToPermission
            } = PermissionTree.methods;
            let keys = permissionToCheckedKeys(
              _.get(member, "role.permission")
            );
            try {
              const permission = JSON.parse(member.permission);
              const disabledKeys = permissionToCheckedKeys(permission.disabled);
              const enabledKeys = permissionToCheckedKeys(permission.enabled);
              keys = _.uniq([
                ..._.difference(keys, disabledKeys),
                ...enabledKeys
              ]);
            } catch (err) {
              // no todo
            }

            const permissions = checkedKeysToPermission(keys);

            _.forEach(permissions, (permission, key) => {
              // 有查看详情权限，才能更改
              if (permission.update && permission.detail) {
                permission.update.fields = _.intersection(
                  permission.update.fields,
                  permission.detail.fields
                );

                permission.update.defaultFields = _.intersection(
                  permission.update.defaultFields,
                  permission.detail.defaultFields
                );
              } else {
                permissions[key] = _.omit(permission, ["update"]);
              }

              if (permission.own) {
                _.forEach(["detail", "list", "search"], key => {
                  if (_.get(permission, `${key}.defaultFields`)) {
                    // 删除查询字段
                    _.pull(
                      permission[key].defaultFields,
                      "created_by_uid",
                      "updated_by_uid"
                    );
                  }
                });
              }
            });

            commit("setValues", permissions);
          }
        }
      });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
