<template>
  <a-cascader
    change-on-select
    v-model="val"
    :field-names="fieldNames"
    :options="options"
    :load-data="loadData"
    :placeholder="languageBase.please_input"
    :popupVisible="popupVisible"
    @click="popupVisible = true"
    @change="change"
    @blur="popupVisible = false"
    @popupVisibleChange="popupVisibleChange"
    style="width:100%"
  />
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { queryAll } from "@/api";

export default {
  name: "data-entry-cascader",
  props: {
    value: [String, Number],
    params: Object
  },
  data() {
    return {
      val: [],
      options: [],
      popupVisible: false
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id")
    }),
    column() {
      return this.allTableColumn[this.params.table_column_id] || {};
    },
    fieldNames() {
      return {
        label: this.column.name || "lable",
        value: "id",
        children: "children"
      };
    }
  },
  watch: {
    column(newVal, oldVal) {
      !_.isEqual(newVal, oldVal) && this.init();
    }
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      !_.isEmpty(this.column) &&
        this.value &&
        this.options.length === 0 &&
        this.getOptions().then(res => {
          const options = _.map(this.params.options, item => ({
            ...item,
            disabled: this.getDisabled(item.id),
            isLeaf: false,
            init: false
          }));
          let option = _.find(options, { id: this.value });
          let ids = [option.id];
          while (option && option.pid && _.indexOf(ids, option.pid) === -1) {
            option = _.find(options, { id: option.pid });
            ids = [option.id, ...ids];
          }

          let child = _.find(options, { pid: ids[0] });
          if (child) {
            option = _.find(res, { id: ids[0] });
            option.children = [child];
            option.init = false;
            _.forEach(ids, id => {
              option = _.find(options, { id });
              child = _.find(options, { pid: id });
              if (child) {
                option.children = [child];
                option.init = false;
              }
            });
          }

          this.options = res;
          this.val = ids;
        });
    },
    getOptions(pid = 0) {
      const stateFilter = ["state", "=", "1"];
      let filters = [[stateFilter, ["pid", "=", pid]]];
      if (!pid) {
        filters = [...filters, [stateFilter, ["pid", "IS", null]]];
      }

      return queryAll({
        table: {
          name: this.column.table.name,
          no_default_field: true,
          fields: ["id", this.column.name],
          order_by: [[this.column.name, "ASC"]],
          filters
        }
      }).then(({ data }) =>
        _.map(data, item => ({
          ...item,
          disabled: this.getDisabled(item.id),
          isLeaf: false
        }))
      );
    },
    popupVisibleChange(value) {
      value &&
        this.options.length === 0 &&
        this.getOptions().then(options => {
          this.options = options;
        });
    },
    loadData(selectedOptions) {
      const targetOption = _.last(selectedOptions);
      if (targetOption.loading) return;

      targetOption.loading = true;
      targetOption.init = true;

      this.getOptions(targetOption.id)
        .then(options => {
          if (!_.isEmpty(options)) {
            targetOption.children = options;
          } else {
            targetOption.isLeaf = true;
            this.popupVisible = false;
          }
        })
        .finally(() => {
          targetOption.loading = false;
          this.options = [...this.options];
        });
    },
    change(value, selectedOptions) {
      const targetOption = _.last(selectedOptions);
      if (targetOption) {
        if (targetOption.isLeaf) {
          this.popupVisible = false;
        } else if (targetOption.init === false) {
          this.loadData(selectedOptions);
        }
      }

      this.$emit("input", _.last(value));
    },
    getDisabled(id) {
      return (
        this.params.tableName === this.column.table.name &&
        this.params.id === id
      );
    }
  }
};
</script>
