import _ from "lodash";
import { queryAll } from "@/api";
import { storeOptions } from "./default";

const tableName = "data_type";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "component", "params"],
        filters: [[["state", "=", 1]]]
      }
    },
    beforeSet({ data }) {
      _.forEach(data, item => {
        try {
          item.params = JSON.parse(item.params);
        } catch (err) {
          item.params = {};
        }
      });
    }
  }
});

export default {
  namespaced: true,
  ...options
};
