<template>
  <a-modal
    :visible="visible"
    :width="width"
    :title="languageBase.detail"
    :ok-text="languageBase.edit"
    :footer="permission.update ? undefined : null"
    @cancel="$emit('close')"
    @ok="$emit('edit')"
  >
    <a-skeleton v-show="detail.loading || !showDetail" active />
    <a-descriptions v-if="showDetail" :column="column" bordered>
      <a-descriptions-item
        v-for="item in items"
        :key="item.key"
        :label="getLabel(item.key)"
        :span="item.span"
      >
        <DataShow
          :value="detail.data[item.key]"
          :component-name="item.componentName"
          :params="item.params"
        />
      </a-descriptions-item>
    </a-descriptions>
  </a-modal>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import DataShow from "./dataShow/index.vue";

export default {
  components: { DataShow },
  props: {
    permission: Object,
    tableName: String
  },
  data() {
    return {
      width: document.body.offsetWidth < 992 ? document.body.offsetWidth : 992,
      // 列在 languageBase 的配置
      LBTC_RE: /^(name|comment|id|state|created_at|updated_at|created_by_uid|updated_by_uid)$/
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id"),
      dataTypes: store => _.keyBy(store.data_type.all.data, "id"),
      detail: store => store.list.detail
    }),
    id() {
      return this.$route.query.id * 1;
    },
    visible() {
      return this.$route.query.id && !!this.$route.query.showDetail;
    },
    showDetail() {
      return !_.isEmpty(this.detail.data);
    },
    tableColumns() {
      if (_.isEmpty(this.permission) || _.isEmpty(this.allTableColumn))
        return [];

      const tableColumns = [];
      _.forEach(_.get(this.permission, "detail.fields"), id => {
        this.allTableColumn[id] && tableColumns.push(this.allTableColumn[id]);
      });
      return _.orderBy(tableColumns, ["index"]);
    },
    fields() {
      return _.isEmpty(this.tableColumns)
        ? []
        : _.uniq([
            "id",
            ..._.map(this.tableColumns, item => item.name),
            ...(_.get(this.permission, "detail.defaultFields") || [])
          ]);
    },
    tableColumnGroup() {
      return _.groupBy(this.tableColumns, item =>
        item.index ? _.floor(item.index) : item.index
      );
    },
    column() {
      let column = 1;
      _.forEach(
        _.uniq([2, ..._.map(this.tableColumnGroup, items => items.length)]),
        val => {
          column *= val;
        }
      );
      return column;
    },
    items() {
      if (_.isEmpty(this.tableColumnGroup) || _.isEmpty(this.detail.data))
        return [];

      const items = [];
      _.forEach(this.tableColumnGroup, (group, groupKey) => {
        const span =
          groupKey === "null" ? this.column : this.column / group.length;

        _.forEach(group, item => {
          const dataType = this.dataTypes[item.data_type.id] || {};
          const params = { ...dataType.params };

          if (dataType.component === "cascader") {
            const column = this.allTableColumn[params.table_column_id];
            params.columnName = column.name;
            params.options = _.get(
              this.detail.extraData,
              `cascader_${column.table.name}`
            );
          }

          items.push({
            key: item.name,
            span,
            componentName: dataType.component,
            params
          });
        });
      });
      const defaultFields = _.get(this.permission, "detail.defaultFields");
      const half = this.column / 2;
      if (_.indexOf(defaultFields, "id") > -1) {
        items.push({
          key: "id",
          span: _.indexOf(defaultFields, "state") > -1 ? half : this.column
        });
      }
      if (_.indexOf(defaultFields, "state") > -1) {
        items.push({
          key: "state",
          span: _.indexOf(defaultFields, "id") > -1 ? half : this.column,
          componentName: "switch"
        });
      }
      if (_.indexOf(defaultFields, "created_at") > -1) {
        items.push({
          key: "created_at",
          span:
            _.indexOf(defaultFields, "updated_at") > -1 ? half : this.column,
          componentName: "datePicker"
        });
      }
      if (_.indexOf(defaultFields, "updated_at") > -1) {
        items.push({
          key: "updated_at",
          span:
            _.indexOf(defaultFields, "created_at") > -1 ? half : this.column,
          componentName: "datePicker"
        });
      }
      if (_.indexOf(defaultFields, "created_by_uid") > -1) {
        items.push({
          key: "created_by_uid",
          span:
            _.indexOf(defaultFields, "updated_by_uid") > -1
              ? half
              : this.column,
          componentName: "user"
        });
      }
      if (_.indexOf(defaultFields, "updated_by_uid") > -1) {
        items.push({
          key: "updated_by_uid",
          span:
            _.indexOf(defaultFields, "created_by_uid") > -1
              ? half
              : this.column,
          componentName: "user"
        });
      }
      return items;
    }
  },
  watch: {
    visible() {
      this.fetchDetail();
    },
    fields() {
      this.fetchDetail();
    }
  },
  mounted() {
    this.fetchDetail();
  },
  methods: {
    ...mapActions("list", {
      getDetail: "getDetail"
    }),
    fetchDetail() {
      if (
        !this.visible ||
        _.isEmpty(this.fields) ||
        (this.tableName === this.fetchDetail.tableName &&
          this.id === this.detail.data.id)
      )
        return;

      this.fetchDetail.tableName = this.tableName;

      this.getDetail({
        table: {
          name: this.tableName,
          no_default_field: true,
          fields: this.fields,
          filters: [[["id", "=", this.id]]]
        }
      });
    },
    getLabel(name) {
      let label = this.languageTableColumn[`${this.tableName}.${name}`];
      return (
        label || (this.LBTC_RE.test(name) && this.languageBase[name]) || name
      );
    }
  }
};
</script>
