<template>
  <a-rate
    v-model="val"
    :allow-half="params.half"
    :auto-focus="params.autoFocus"
  />
</template>

<script>
export default {
  name: "data-entry-rate",
  props: {
    value: Number,
    params: Object
  },
  data() {
    return {
      val: this.value
    };
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.val = newVal;
      }
    },
    val(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit("input", newVal);
      }
    }
  }
};
</script>
