<template>
  <a-checkbox-group v-model="val" :options="options" />
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { queryAll } from "@/api";

export default {
  name: "data-entry-checkbox",
  props: {
    value: Array,
    params: Object
  },
  data() {
    return {
      val: this.value,
      options: []
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id"),
      allTableRelated: store => _.keyBy(store.table_related.all.data, "id")
    }),
    column() {
      if (_.isEmpty(this.allTableRelated)) return {};
      const tr = this.allTableRelated[this.params.table_related_id];
      const columnId = tr[`t${this.params.index}_column`].id;
      return this.allTableColumn[columnId];
    },
    selected() {
      return _.map(this.val, value => ({
        id: value,
        [this.column.name]: _.find(this.options, { value }).label
      }));
    }
  },
  watch: {
    val(newVal, oldVal) {
      if (!_.isEqual(newVal, oldVal)) {
        this.$emit("input", newVal);
      }
    }
  },
  mounted() {
    if (_.isEmpty(this.allTableRelated)) {
      this.getTableRelated().then(this.getOptions);
    } else {
      this.getOptions();
    }
  },
  methods: {
    ...mapActions("table_related", {
      getTableRelated: "getAll"
    }),
    getOptions() {
      queryAll({
        table: {
          name: this.column.table.name,
          no_default_field: true,
          fields: ["id", this.column.name],
          filters: [[["state", "=", 1]]]
        }
      }).then(({ data }) => {
        this.options = _.map(data, item => ({
          label: item[this.column.name],
          value: item.id
        }));
      });
    }
  }
};
</script>
