<template>
  <span @click="initList">
    <a-select
      allow-clear
      show-search
      v-model="val"
      mode="multiple"
      :default-active-first-option="false"
      :filter-option="false"
      :loading="fetching"
      :placeholder="languageBase.please_select"
      @search="handleSearch"
      @popupScroll="popupScroll"
      style="width: 100%"
    >
      <a-select-option
        v-for="(item, index) in list.data"
        :key="index"
        :value="item.id"
      >
        <a-avatar v-if="item.avatar" :src="item.avatar | cdn" size="small" />
        {{ item.name || `uid(${item.id})` }}
      </a-select-option>
      <a-select-option v-if="fetching" value="fetching" disabled>
        <a-spin size="small" />
      </a-select-option>
    </a-select>
  </span>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { queryList } from "@/api/account";

export default {
  name: "data-entry-user",
  props: {
    value: Array,
    params: Object
  },
  data() {
    this.handleSearch = _.debounce(this.handleSearch, 500);

    return {
      val: this.value,
      fetching: false,
      list: { data: [] }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    value(val) {
      if (!_.isEqual(val, this.val)) {
        this.val = val;
      }
    },
    val(val) {
      if (!_.isEqual(val, this.value)) {
        this.$emit("input", val);
      }
    }
  },
  methods: {
    fetchList(params) {
      this.fetching = true;
      this.fetchList.params = params;

      if (params.current === 1) {
        this.list = { data: [] };
      }

      queryList(params)
        .then(res => {
          if (params === this.fetchList.params) {
            if (res.current === 1) {
              this.list = {
                ...res,
                pages: _.ceil(res.total / res.pageSize)
              };
            } else {
              this.list = {
                ...this.list,
                current: res.current,
                data: [...this.list.data, ...res.data]
              };
            }
          }
        })
        .finally(() => {
          if (params === this.fetchList.params) {
            this.fetching = false;
          }
        });
    },
    handleParams(searchValue) {
      const params = {
        current: 1,
        table: {
          name: "user",
          no_default_field: true,
          fields: ["id", "name", "avatar"],
          order_by: [["name", "ASC"]],
          filters: [[["state", "=", 1]]]
        }
      };

      if (searchValue) {
        const filter = ["name", "LIKE", `%${searchValue}%`];
        params.table.filters = [[["state", "=", 1], filter]];
      }

      if (this.params.filters) {
        params.table.filters[0] = [
          ...params.table.filters[0],
          ...this.params.filters
        ];
      }

      return params;
    },
    handleSearch(value) {
      this.fetchList(this.handleParams(value));
    },
    initList() {
      this.params.filters && this.fetchList(this.handleParams());
    },
    popupScroll(e) {
      if (this.fetching) return;

      const { scrollTop, clientHeight, scrollHeight } = e.target;

      if (
        scrollTop + clientHeight + 1 > scrollHeight &&
        this.list.pages > this.list.current
      ) {
        this.fetchList({
          ...this.fetchList.params,
          current: this.list.current + 1
        });
      }
    }
  }
};
</script>
