<template>
  <div style="height: 104px">
    <a-upload
      list-type="picture-card"
      accept="image/jpeg, image/jpg, image/png, image/webp"
      :action="action"
      :file-list="fileList"
      :before-upload="beforeUpload"
      :data="handleData"
      @change="handleChange"
      @preview="previewVisible = true"
    >
      <div v-if="!fileList.length">
        <a-icon type="plus" />
        <div class="ant-upload-text">
          {{ params.width }} x {{ params.height }}
        </div>
      </div>
    </a-upload>
    <a-modal
      :visible="previewVisible"
      :footer="null"
      :width="params.width + 48"
      @cancel="previewVisible = false"
    >
      <img style="width: 100%" :src="previewImage" />
    </a-modal>
  </div>
</template>

<script>
import _ from "lodash";
import { fileH, fileToUrl } from "@/utils";
import { origins } from "@/dict";
import { handleImage } from "./utils/upload";

export default {
  name: "data-entry-image",
  props: {
    value: String,
    params: Object
  },
  data() {
    const fileList = [];
    if (this.value) {
      fileList[0] = {
        url: this.$options.filters.cdn(this.value),
        status: "done",
        uid: this.value,
        name: this.value + ".webp"
      };
    }
    return {
      action: `${origins.OSS}/upload`,
      fileList,
      previewVisible: false
    };
  },
  computed: {
    previewImage() {
      return _.get(this.fileList, "[0].url");
    }
  },
  methods: {
    beforeUpload(file) {
      if (!/^image\/(jpeg|jpg|png|webp)$/.test(file.type)) {
        this.$message.error("只能上传jpg、jpg、png、webp格式图片");
        return false;
      }

      const { uid, name } = file;

      return new Promise((resolve, reject) => {
        handleImage({ ...this.params, file })
          .then(file => {
            fileToUrl(file)
              .then(src => {
                file.url = src;
                file.uid = uid;
                file.name = name;

                if (file.status === "done") {
                  this.fileList = [file];
                  this.$emit("input", file.hash);
                  return reject();
                }

                resolve(file);
              })
              .catch(reject);
          })
          .catch(reject);
      });
    },
    handleData(file) {
      return {
        h: file.hash,
        s: fileH(file.hash).toString()
      };
    },
    handleChange({ file, fileList }) {
      this.fileList = fileList;
      if (file.status === "done") {
        this.$emit("input", file.hash);
      }
    }
  }
};
</script>
