<template>
  <a-input-number
    v-model="val"
    :precision="params.precision"
    :step="params.step"
    :min="params.min"
    :max="params.max"
    :placeholder="languageBase.please_input"
    :auto-focus="params.autoFocus"
    style="width:100%"
    @blur="$emit('blur', val)"
  />
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "data-entry-inputNumber",
  props: {
    value: Number,
    params: Object
  },
  data() {
    return {
      val: this.value
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.val = newVal;
      }
    },
    val(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit("input", newVal);
      }
    }
  }
};
</script>
