import _ from "lodash";
import { setLocalStorageItem, getLocalStorageItem } from "@/utils";
import { queryAll } from "@/api";
import { storeOptions } from "./default";
import { SID } from "@/dict";

const tableName = "locale";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "code", "native_name"],
        filters: [[["state", "=", 1]]]
      }
    }
  }
});

const LS_NAME = `locale_${SID}`;

options.state.selected = getLocalStorageItem(LS_NAME) || {
  id: 50,
  code: "zh_CN",
  native_name: "简体中文"
};

options.mutations.select = (state, locale) => {
  locale = _.pick(locale, ["id", "code", "native_name"]);
  if (locale.id !== state.selected.id) {
    state.selected = locale;
    setLocalStorageItem(LS_NAME, locale);
  }
};

export default {
  namespaced: true,
  ...options
};
