<template>
  <div class="rich-text" :class="{ 'zh-cn': selectedLocale.id === 50 }">
    <quill-editor
      v-model="content"
      ref="editor"
      :options="{
        placeholder: languageBase.please_input
      }"
      @change="onEditorChange($event)"
      @blur="$emit('blur', content)"
    />
    <UploadImage ref="uploadImage" v-show="false" multiple />
  </div>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import UploadImage from "./uploadImage";

export default {
  name: "data-entry-richText",
  components: {
    UploadImage
  },
  props: {
    value: {
      type: String
    }
  },
  data() {
    let content;
    if (this.value) {
      const div = document.createElement("div");
      div.innerHTML = this.value;
      const imgs = div.getElementsByTagName("img");
      _.forEach(imgs, img => {
        img.src = this.$options.filters.cdn(img.dataset.hash);
      });
      content = div.innerHTML;
    }
    return {
      content
    };
  },
  computed: {
    ...mapState({
      selectedLocale: store => store.locale.selected,
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    selectedLocale({ id }) {
      this.$refs.editor.quill.theme.tooltip.root.querySelector(
        "input[data-video]"
      ).dataset.video = id === 50 ? "嵌入网址" : "Embed URL";
    }
  },
  mounted() {
    const { quill } = this.$refs.editor;
    const { root } = quill.theme.tooltip;
    quill.getModule("toolbar").addHandler("image", this.imageHandler);
    root.querySelector("input[data-link]").dataset.link = location.origin;
    root.querySelector("input[data-video]").dataset.video =
      this.selectedLocale.id === 50 ? "嵌入网址" : "Embed URL";
  },
  methods: {
    onEditorChange({ html }) {
      const div = document.createElement("div");
      div.innerHTML = html;
      const imgs = div.getElementsByTagName("img");
      _.forEach(imgs, img => {
        img.dataset.hash = img.src.replace(
          /.+\/(.{2})\/(.{2})\/(.{28})$/,
          "$1$2$3"
        );
        img.removeAttribute("src");
      });
      this.$emit("input", div.innerHTML);
    },
    imageHandler() {
      const { quill } = this.$refs.editor;
      const range = quill.getSelection();
      this.$refs.uploadImage.click(file => {
        const src = !file.arrayBuffer
          ? this.$options.filters.oss(file.hash)
          : this.$options.filters.cdn(file.hash);
        quill.insertEmbed(range.index, "image", src);
        ++range.index;
        quill.setSelection(range);
      });
    }
  }
};
</script>

