<template>
  <a-input
    v-model="val"
    :allowClear="true"
    :auto-focus="params.autoFocus"
    :disabled="params.disabled"
    :placeholder="languageBase.please_input"
    @blur="$emit('blur', val)"
  />
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "data-entry-input",
  props: {
    value: String,
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      val: this.value
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.val = newVal;
      }
    },
    val(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit("input", newVal);
      }
    }
  }
};
</script>
