<template>
  <a-table
    :columns="tableColumns"
    :data-source="list.data"
    :loading="list.loading"
    :pagination="pagination"
    :scroll="{ x: true }"
    rowKey="id"
    class="table-base"
    bordered
    @change="handleChange"
  >
    <template v-if="!$scopedSlots.title" v-slot:title>
      <a-row
        v-if="!$scopedSlots.title"
        type="flex"
        justify="space-between"
        align="middle"
      >
        <div>{{ languageTable[tableName] || tableName }}</div>
        <a-button
          v-if="create"
          type="primary"
          icon="plus"
          :title="languageBase.create"
          @click="$emit('show-edit')"
        />
      </a-row>
    </template>

    <!-- eslint-disable-next-line -->
    <template v-if="!$scopedSlots.state" v-slot:state="text, record">
      <a-switch
        :checked-children="languageBase.enable"
        :un-checked-children="languageBase.disable"
        :disabled="getStateDisabled(record)"
        :checked="!!text"
        :loading="record.saveLoading"
        @change="$emit('state-change', { id: record.id, state: !text })"
      />
    </template>

    <template v-if="!$scopedSlots.unix" v-slot:unix="text">
      {{ text | unixFormat }}
    </template>

    <!-- eslint-disable-next-line -->
    <template v-for="(item, key) in $scopedSlots" v-slot:[key]="text, record, index">
      <span v-if="key !== 'operation'" :key="key">
        <slot
          :name="key"
          v-bind:text="text"
          v-bind:record="record"
          v-bind:index="index"
        />
      </span>
    </template>

    <!-- eslint-disable-next-line -->
    <template v-if="edit || del || $scopedSlots.operation" v-slot:operation="text, record, index">
      <a-space>
        <a-button
          v-if="detail"
          icon="eye"
          size="small"
          :title="languageBase.detail"
          @click="$emit('show-detail', record)"
        />
        <a-button
          v-if="edit"
          type="primary"
          icon="edit"
          size="small"
          :disabled="record.auth === 1"
          :title="languageBase.edit"
          @click="$emit('show-edit', record)"
        />
        <a-popconfirm
          v-if="del"
          key="delete"
          :disabled="getDelDisabled(record)"
          :title="languageBase.sure_delete"
          @confirm="$emit('delete', record)"
        >
          <a-button
            type="danger"
            icon="delete"
            size="small"
            :disabled="getDelDisabled(record)"
            :title="languageBase.delete"
          />
        </a-popconfirm>
        <slot
          name="operation"
          v-bind:text="text"
          v-bind:record="record"
          v-bind:index="index"
        />
      </a-space>
    </template>
  </a-table>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";

export default {
  props: {
    columns: Array,
    list: Object,
    tableName: String,
    create: Boolean,
    detail: Boolean,
    edit: Boolean,
    del: Boolean,
    stateDisabled: Function,
    delDisabled: Function
  },
  data() {
    return {
      // 列在 languageBase 的配置
      LBDTC_RE: /^(name|state|created_at|updated_at|comment|icon|created_by_uid|updated_by_uid|operation|show)$/
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      languageTableColumn: store => store.languageTableColumn.values,
      loading: store =>
        store.languageBase.all.loading ||
        store.languageTable.all.loading ||
        store.languageTableColumn.all.loading
    }),
    pagination() {
      return (
        this.list.pagination && {
          ...this.list.pagination,
          showTotal: total =>
            _.template(this.languageBase.total_template)({ total })
        }
      );
    },
    tableColumns() {
      if (this.loading || this.list.loading) return [];

      const tableColumns = _.map(this.columns, item => ({
        ...item,
        key: item.key || item.dataIndex
      }));

      if (
        !_.find(tableColumns, { key: "operation" }) &&
        (this.edit || this.del || this.$scopedSlots.operation)
      ) {
        tableColumns.push({
          key: "operation",
          scopedSlots: { customRender: "operation" }
        });
      }

      return _.map(tableColumns, item => {
        let title;
        if (/\./.test(item.key)) {
          title = this.getLTableColumnTitle(...item.key.split("."));
        } else {
          title = this.languageTableColumn[`${this.tableName}.${item.key}`];
        }

        if (!title && this.LBDTC_RE.test(item.key)) {
          title = this.languageBase[item.key];
        }

        return { ...item, title: title || item.title || item.key };
      });
    }
  },
  methods: {
    getLTableColumnTitle(tableName, tableColumnName) {
      const table = this.languageTable[tableName];
      const tableColumn = this.languageTableColumn[
        `${tableName}.${tableColumnName}`
      ];
      return table && tableColumn ? `${table} - ${tableColumn}` : "";
    },
    handleChange(pagination, filters, sorter) {
      this.$emit("change", pagination, filters, sorter);
    },
    getStateDisabled(record) {
      return this.stateDisabled && this.stateDisabled(record);
    },
    getDelDisabled(record) {
      return this.delDisabled && this.delDisabled(record);
    }
  }
};
</script>

<style lang="stylus">
.table-base
  td
    white-space nowrap
  th
    white-space nowrap
  p
    margin-bottom 0
</style>
