<template>
  <a-drawer
    :title="title"
    :maskClosable="false"
    :visible="visible"
    :width="width"
    @close="close"
    :headerStyle="{ textAlign: 'center' }"
  >
    <a-spin v-show="loading" />
    <a-form-model v-if="!loading" @submit="handleSubmit" @submit.native.prevent>
      <a-form-model-item>
        <PermissionTree ref="permissionTree" :permission="detail.permission" />
      </a-form-model-item>
      <a-form-model-item>
        <a-button
          block
          type="primary"
          html-type="submit"
          :loading="submitLoding"
        >
          {{ languageBase.confirm }}
        </a-button>
      </a-form-model-item>
    </a-form-model>
  </a-drawer>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { queryAll, save } from "@/api";
import PermissionTree from "./PermissionTree.vue";

export default {
  components: {
    PermissionTree
  },
  data() {
    return {
      visible: false,
      title: "",
      width: document.body.offsetWidth < 450 ? document.body.offsetWidth : 450,
      submitLoding: false,
      detail: {},
      loading: true
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values
    })
  },
  methods: {
    show(role) {
      this.detail = role;
      this.title = `${this.languageTable.role} - ${role.name} - ${this.languageBase.permission}`;
      this.visible = true;
      this.loading = true;
      queryAll({
        table: {
          name: "role",
          no_default_field: true,
          fields: ["permission"],
          filters: [[["id", "=", role.id]]]
        }
      }).then(({ data }) => {
        this.detail.permission = data[0].permission;
        this.loading = false;
      });
    },
    close() {
      this.visible = false;
    },
    handleSubmit() {
      const {
        checkedKeys,
        roleCheckedKeys,
        checkedKeysToPermission
      } = this.$refs.permissionTree;

      if (
        _.isEmpty(
          _.omitBy(_.xor(checkedKeys, roleCheckedKeys), key =>
            /\d+-(detail|update|list|search)$/.test(key)
          )
        )
      ) {
        this.close();
        return;
      }

      save({
        table: { name: "role" },
        rows: [
          {
            id: this.detail.id,
            permission: _.isEmpty(checkedKeys)
              ? null
              : JSON.stringify(checkedKeysToPermission(checkedKeys))
          }
        ]
      }).then(() => {
        this.$notification.success({
          message: this.languageBase.save,
          comment: this.languageBase.successful
        });

        _.forOwn(sessionStorage, (value, key) => {
          const RE = new RegExp(`^q-member-`);
          RE.test(key) && sessionStorage.removeItem(key);
        });

        this.close();
      });
    }
  }
};
</script>
