import moment from "moment";

const ACCOUNT_TOKEN_KEY = "account_token";
const SYSTEM_TOKEN_KEY_BASE = "system_token_";

export const getTokenPayload = token => {
  let payload = {};
  try {
    payload = JSON.parse(atob((token + "").replace(/(.+)\..+/, "$1")));
  } catch (err) {
    window.console.error(err);
  }
  return payload;
};

// account ---- start
export const verifyTokenExp = token =>
  getTokenPayload(token).exp > moment().unix() + 1800;

export const getAccountToken = () => {
  const token = localStorage.getItem(ACCOUNT_TOKEN_KEY);

  return !token || !verifyTokenExp(token) ? "" : token;
};

export const setAccountToken = token => {
  if (!token || !verifyTokenExp(token)) return false;

  localStorage.setItem(ACCOUNT_TOKEN_KEY, token);
  return true;
};

export const getAccountTokenId = () => {
  const token = getAccountToken();
  return token ? getTokenPayload(token).id : 0;
};

export const delAccountToken = () => localStorage.removeItem(ACCOUNT_TOKEN_KEY);
// account ---- end

// system ---- start
export const verifyToken = token => {
  const payload = getTokenPayload(token);
  if (!payload.exp || !payload.uid || !payload.sid) return false;
  return payload.exp > moment().unix() + 1800;
};

export const getToken = sid => {
  const token = localStorage.getItem(`${SYSTEM_TOKEN_KEY_BASE}${sid}`);

  return !token || !verifyToken(token) ? "" : token;
};

export const getTokenUid = sid => {
  const token = getToken(sid);
  return token ? getTokenPayload(token).uid : 0;
};

export const setToken = (sid, token) => {
  if (!verifyToken(token)) return false;

  localStorage.setItem(`${SYSTEM_TOKEN_KEY_BASE}${sid}`, token);
  return true;
};

export const delToken = sid =>
  localStorage.removeItem(`${SYSTEM_TOKEN_KEY_BASE}${sid}`);
// system ---- end
