export const DOMAIN = location.hostname.replace(/.+?([a-z]+\.cn)$/, "$1");
export const SID = (location.pathname.match(/^\/?(\d+)/) || [])[1] * 1;

export const hosts = {
  OSS: `oss.${DOMAIN}`,
  OOS: `oos.${DOMAIN}`,
  FILE: `file.${DOMAIN}`
};
Object.freeze(hosts);

export const origins = {
  ACCOUNT_API: `${location.protocol}//account-api.${hosts.OOS}`,
  ACCOUNT_FRONTEND: `${location.protocol}//account.${hosts.OOS}`,
  SYSTEM_API: `${location.protocol}//system-api.${hosts.OOS}`,
  OSS: `${location.protocol}//${hosts.OSS}`,
  FILE: `${location.protocol}//${hosts.FILE}`,
  CDN: `https://cdn.jsdelivr.net/gh/lin09/files`
};
Object.freeze(origins);
