<template>
  <a-range-picker
    v-model="val"
    :format="params.format"
    :showTime="showTime"
    style="min-width:100%"
  />
</template>

<script>
import _ from "lodash";
import moment from "moment";

export default {
  name: "data-entry-rangePicker",
  props: {
    value: Array,
    params: Object
  },
  data() {
    return {
      val: _.map(this.value, moment.unix)
    };
  },
  computed: {
    showTime() {
      return /lll|LLL|H|h|m|s/.test(this.params.format);
    }
  },
  watch: {
    val(newVal, oldVal) {
      newVal = this.toUnix(newVal);
      oldVal = this.toUnix(oldVal);
      !_.isEqual(newVal, oldVal) && this.$emit("input", newVal);
    },
    value(newVal, oldVal) {
      if (
        !_.isEqual(newVal, oldVal) &&
        !_.isEqual(newVal, this.toUnix(this.val))
      ) {
        this.val = _.map(newVal, moment.unix);
      }
    }
  },
  methods: {
    toUnix(val) {
      return _.map(val, (item, index) =>
        this.showTime
          ? item.unix()
          : index === 0
          ? item
              .hour(0)
              .minute(0)
              .second(0)
              .unix()
          : item
              .hour(23)
              .minute(59)
              .second(59)
              .unix()
      );
    }
  }
};
</script>
