<template>
  <span>
    <a-spin v-if="loading" />
    <a-tree v-else checkable v-model="checkedKeys" :tree-data="treeData" />
  </span>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";

export default {
  name: "PermissionTree",
  props: {
    value: Array,
    permission: String,
    differencePermission: String
  },
  data() {
    return {
      checkedKeys: [],
      roleCheckedKeys: [],
      oldCheckedKeys: []
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTable: store => store.languageTable.values,
      languageTableColumn: store => store.languageTableColumn.values,
      languageMenuGroup: store => store.languageMenuGroup.values,
      tables: store => _.filter(store.table.all.data, item => item.id > 10),
      tableColumns: store => store.table_column.all.data,
      dataTypes: store => _.keyBy(store.data_type.all.data, "id"),
      loading: store =>
        store.languageBase.all.loading ||
        store.languageTable.all.loading ||
        store.languageTableColumn.all.loading ||
        store.languageMenuGroup.all.loading
    }),
    treeData() {
      if (this.loading || _.isEmpty(this.tableColumns)) return [];

      const treeData = _.map(this.tables, item => {
        const fields = _.map(
          _.filter(this.tableColumns, { table: { id: item.id } }),
          field => ({
            ...field,
            title:
              this.languageTableColumn[`${item.name}.${field.name}`] ||
              this.languageBase[field.name] ||
              field.name
          })
        );
        const children = [
          {
            key: `${item.id}-create`,
            title: this.languageBase.create,
            isLeaf: true
          },
          {
            key: `${item.id}-del`,
            title: this.languageBase.delete,
            isLeaf: true
          },
          {
            key: `${item.id}-own`,
            title: this.languageBase.query_only_own,
            isLeaf: true
          },
          this.getTypeChildren("detail", item, fields),
          this.getTypeChildren("update", item, fields),
          this.getTypeChildren("list", item, fields),
          this.getTypeChildren("search", item, fields)
        ];

        if (/^(member|role)$/.test(item.name)) {
          children.push({
            key: `${item.id}-permission`,
            title: this.languageBase.permission,
            isLeaf: true
          });
        }

        return {
          name: item.name,
          key: item.id,
          title: this.languageTable[item.name] || item.name,
          menu_group: item.menu_group,
          children
        };
      });

      const dataGroup = _.groupBy(treeData, "menu_group.id");

      return [
        ...(dataGroup.null || []),
        ..._.map(_.omit(dataGroup, ["null"]), item => ({
          key: `menu_group-${item[0].menu_group.id}`,
          title:
            this.languageMenuGroup[item[0].menu_group.name] ||
            item[0].menu_group.name,
          children: item
        }))
      ];
    }
  },
  watch: {
    checkedKeys() {
      this.$emit("input", this.checkedKeys);
    },
    permission() {
      this.handlePermission();
    },
    differencePermission() {
      this.handlePermission();
    },
    treeData() {
      this.handlePermission();
    }
  },
  mounted() {
    this.handlePermission();
  },
  methods: {
    getTypeChildren(type, item, fields) {
      if (/^(member|role)$/.test(item.name)) {
        fields = _.filter(fields, field => field.name !== "permission");
      }
      if (type === "search") {
        fields = _.filter(
          fields,
          field =>
            !/^(avatar|upload|image|images)$/.test(
              this.dataTypes[field.data_type.id].component
            )
        );
      }
      if (item.name === "member" && type === "update") {
        fields = _.filter(fields, field => field.name !== "uid");
      }

      const key = `${item.id}-${type}`;
      const res = {
        key,
        title: this.languageBase[type],
        children: [
          ..._.map(fields, field => ({
            key: `${key}-${field.id}`,
            title: field.title,
            isLeaf: true
          })),
          this.getDefaultField(key, "state")
        ]
      };

      if (type !== "update") {
        res.children = [
          ...res.children,
          this.getDefaultField(key, "id"),
          this.getDefaultField(key, "created_at"),
          this.getDefaultField(key, "updated_at"),
          this.getDefaultField(key, "created_by_uid"),
          this.getDefaultField(key, "updated_by_uid")
        ];
      }
      return res;
    },
    getDefaultField(key, name) {
      return {
        key: `${key}-${name}`,
        title: this.languageBase[name] || name,
        isLeaf: true
      };
    },
    handlePermission() {
      this.roleCheckedKeys = this.permissionToCheckedKeys(this.permission);
      this.checkedKeys = [...this.roleCheckedKeys];

      try {
        const permission = JSON.parse(this.differencePermission);
        const disabledKeys = this.permissionToCheckedKeys(permission.disabled);
        const enabledKeys = this.permissionToCheckedKeys(permission.enabled);
        this.checkedKeys = _.uniq([
          ..._.difference(this.checkedKeys, disabledKeys),
          ...enabledKeys
        ]);
        this.oldCheckedKeys = [...this.checkedKeys];
      } catch (err) {
        // no todo
      }
    },
    permissionToCheckedKeys(permission) {
      let checkedKeys = [];
      try {
        if (_.isString(permission)) {
          permission = JSON.parse(permission);
        }
      } catch (err) {
        permission = {};
      }

      _.forEach(permission, (items, tableId) => {
        _.forEach(items, (value, key) => {
          key = `${tableId}-${key}`;
          if (
            value === 1 ||
            (value.checked &&
              _.isEmpty(value.fields) &&
              _.isEmpty(value.defaultFields))
          ) {
            checkedKeys.push(key);
          } else {
            checkedKeys = [
              ...checkedKeys,
              ..._.map(value.defaultFields, field => `${key}-${field}`),
              ..._.map(value.fields, field => `${key}-${field}`)
            ];
          }
        });
      });

      return checkedKeys;
    },
    checkedKeysToPermission(keys) {
      const permission = {};

      _.forEach(keys, checkedKey => {
        if (/^[0-9]+-/.test(checkedKey)) {
          let [tableId, type, tableColumn] = _.split(checkedKey, "-");
          tableId *= 1;
          permission[tableId] = { ...permission[tableId] };
          if (/^(create|del|permission|own)$/.test(type)) {
            permission[tableId][type] = 1;
          } else if (tableColumn) {
            permission[tableId][type] = { ...permission[tableId][type] };

            if (/^[0-9]+$/.test(tableColumn)) {
              permission[tableId][type].fields = [
                ...(permission[tableId][type].fields || []),
                tableColumn * 1
              ];
            } else {
              permission[tableId][type].defaultFields = [
                ...(permission[tableId][type].defaultFields || []),
                tableColumn
              ];
            }
          }
        }
      });

      return _.omitBy(permission, _.isEmpty);
    }
  }
};
</script>
