<template>
  <div>
    <Filters
      v-if="permission.search"
      :tableName="tableName || storeModule"
      :field="permission.search"
      :loading="list.loading"
      @submit="handleFilters"
    />

    <a-modal v-model="richTextVisible" :width="richTextWidth" :footer="null">
      <DataShow componentName="richText" :value="richTextContent" />
    </a-modal>

    <a-modal v-model="fileListVisible" :footer="null">
      <DataShow componentName="upload" :value="filesJson" />
    </a-modal>

    <TableBase
      :tableName="tableName || storeModule"
      :columns="columnInfo.columns"
      :list="list"
      :create="permission.create === 1"
      :edit="editEnabled"
      :detail="!!permission.detail"
      :del="permission.del === 1"
      :stateDisabled="stateDisabled"
      :delDisabled="delDisabled"
      @change="handleChange"
      @state-change="handleChangeItem"
      @delete="handleDelete"
      @show-edit="data => $emit('show-edit', data)"
      @show-detail="data => $emit('show-detail', data)"
    >
      <template
        v-for="item in columnInfo.switchs"
        v-slot:[`switch-${item.key}`]="{ text, record }"
      >
        <a-switch
          :key="item.key"
          :checked-children="languageBase.opened"
          :un-checked-children="languageBase.closed"
          :checked="!!text"
          :disabled="item.disabled"
          :loading="record.saveLoading"
          @change="handleChangeItem({ id: record.id, [item.key]: !text })"
        />
      </template>

      <template v-for="(item, key) in dataShows" v-slot:[key]="{ text }">
        <DataShow
          :key="key"
          :componentName="item.componentName"
          :value="text"
          :params="item.params"
          style="font-size: 14px"
        />
      </template>

      <template v-slot:richText="{ text }">
        <a-icon
          v-if="text"
          type="file-word"
          :style="{ fontSize: '20px', color: '#185ABD' }"
          @click="showRichTextModal(text)"
        />
        <span v-else>-</span>
      </template>

      <template v-slot:fileList="{ text }">
        <a-icon
          v-if="text && text !== 'null'"
          type="paper-clip"
          :style="{ fontSize: '20px' }"
          @click="showFileListModal(text)"
        />
        <span v-else>-</span>
      </template>

      <template
        v-for="(item, name) in $scopedSlots"
        v-slot:[name]="{ text, record, index }"
      >
        <span :key="name">
          <slot
            :name="name"
            v-bind:text="text"
            v-bind:record="record"
            v-bind:index="index"
          />
        </span>
      </template>
    </TableBase>
  </div>
</template>

<script>
import _ from "lodash";
import { mapState, mapActions } from "vuex";
import { getAccountTokenId } from "@/utils";
import Filters from "./Filters";
import TableBase from "./TableBase";
import DataShow from "./dataShow/index.vue";

export default {
  components: {
    Filters,
    TableBase,
    DataShow
  },
  props: {
    storeModule: String,
    tableName: String,
    permission: Object,
    filters: Object
  },
  data() {
    return {
      uid: getAccountTokenId(),
      richTextVisible: false,
      richTextContent: "",
      richTextWidth:
        document.body.offsetWidth < 768 ? document.body.offsetWidth : 768,
      fileListVisible: false,
      filesJson: "",
      dataShows: {
        dataShowDefault: {},
        dataShowUser: {
          componentName: "user",
          params: { size: "small" }
        },
        dataShowAvatar: {
          componentName: "avatar",
          params: { size: "small" }
        },
        dataShowRate: {
          componentName: "rate"
        }
      },
      detaultColumns: {
        id: {
          dataIndex: "id",
          sorter: true
        },
        created_at: {
          dataIndex: "created_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        updated_at: {
          dataIndex: "updated_at",
          scopedSlots: { customRender: "unix" },
          sorter: true
        },
        state: {
          dataIndex: "state",
          scopedSlots: { customRender: "state" },
          sorter: true
        },
        created_by_uid: {
          dataIndex: "created_by_uid",
          scopedSlots: { customRender: "dataShowUser" }
        },
        updated_by_uid: {
          dataIndex: "updated_by_uid",
          scopedSlots: { customRender: "dataShowUser" }
        }
      }
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id"),
      dataTypes: store => _.keyBy(store.data_type.all.data, "id"),
      list(store) {
        return store[this.storeModule].list;
      }
    }),
    tableColumns() {
      return _.keyBy(
        _.filter(this.allTableColumn, {
          table: { name: this.tableName }
        }),
        "name"
      );
    },
    getListActionName() {
      return this.storeModule + "/getList";
    },
    saveActionName() {
      return this.storeModule + "/save";
    },
    visibleFilters() {
      return !_.isEmpty(this.filters);
    },
    params() {
      if (_.isEmpty(this.permission.list) || _.isEmpty(this.allTableColumn))
        return undefined;

      const fields = [
        ...(this.permission.list.defaultFields || []),
        ..._.filter(this.permission.list.fields, id => this.allTableColumn[id])
      ];

      return _.isEmpty(fields)
        ? undefined
        : {
            table: {
              name: this.tableName,
              no_default_field: true,
              fields: _.uniq([...fields, "id"])
            }
          };
    },
    columnInfo() {
      if (
        _.isEmpty(this.permission.list) ||
        _.isEmpty(this.allTableColumn) ||
        _.isEmpty(this.dataTypes)
      )
        return [];

      const columns = [];
      const switchs = [];
      _.forEach(this.permission.list.fields, id => {
        const column = this.allTableColumn[id];
        if (column) {
          let sorter = false;
          const scopedSlots = { customRender: "dataShowDefault" };
          const { component, params } = this.dataTypes[column.data_type.id];

          if (/^(switch|input|inputNumber|rate|datePicker)$/.test(component)) {
            sorter = true;
          }

          if (this.tableName === "member" && column.name === "uid") {
            scopedSlots.customRender = "dataShowUser";
          } else if (component === "switch") {
            scopedSlots.customRender = `switch-${column.name}`;
            switchs.push({
              key: column.name,
              disabled:
                _.indexOf(
                  _.get(this.permission.update, "fields"),
                  column.id
                ) === -1
            });
          } else if (component === "avatar") {
            scopedSlots.customRender = "dataShowAvatar";
          } else if (component === "rate") {
            scopedSlots.customRender = "dataShowRate";
          } else if (/^(datePicker|image)$/.test(component)) {
            scopedSlots.customRender = `dataShow-${column.name}`;
            this.dataShows[scopedSlots.customRender] = {
              componentName: component,
              params
            };
          } else if (component === "richText") {
            scopedSlots.customRender = "richText";
          } else if (component === "upload") {
            scopedSlots.customRender = "fileList";
          }

          columns.push({
            dataIndex: column.name,
            scopedSlots,
            sorter
          });
        }
      });
      _.forEach(this.permission.list.defaultFields, field => {
        const column = this.detaultColumns[field];
        field !== "id" ? columns.push(column) : columns.unshift(column);
      });
      return { columns, switchs };
    },
    editEnabled() {
      return _.isEmpty(this.permission.update)
        ? false
        : !this.permission.update.fields ||
            this.permission.update.fields.length !== 1 ||
            !/^(role|member)$/.test(this.tableName) ||
            this.permission.update.fields[0] !==
              this.tableColumns.permission.id;
    }
  },
  watch: {
    tableName() {
      this.handleFilters.filters = [];
    },
    params(newVal, oldVal) {
      !_.isEmpty(newVal) &&
        !_.isEqual(newVal, oldVal) &&
        this.getList({ current: 1, pageSize: 10 });
    }
  },
  created() {
    this.getAllDataType();
    this.getList({ current: 1, pageSize: 10 });
  },
  methods: {
    ...mapActions("data_type", {
      getAllDataType: "getAll"
    }),
    handleSave(record) {
      return this.$store.dispatch(this.saveActionName, {
        tableName: this.tableName,
        record
      });
    },
    handleChangeItem(record) {
      this.handleSave(record).then(() => {
        this.$notification.success({
          message: this.languageBase.save,
          description: this.languageBase.successful
        });
        this.$emit("save", record);
      });
    },
    handleDelete(record) {
      this.handleSave({
        id: record.id,
        deleted_at: 1
      }).then(() => {
        this.$notification.success({
          message: this.languageBase.delete,
          description: this.languageBase.successful
        });
        this.$emit("delete", record);
      });
    },
    getList(params) {
      params = _.mergeWith(_.cloneDeep(this.params), params);

      if (_.isEmpty(_.get(params, "table.order_by"))) {
        _.set(params, "table.order_by", [["id", "DESC"]]);
      }

      if (!_.isEmpty(this.handleFilters.filters)) {
        const filters = _.get(params, "table.filters");
        if (_.isEmpty(filters)) {
          _.set(params, "table.filters[0]", this.handleFilters.filters);
        } else {
          _.forEach(filters, (value, index) => {
            _.set(params, `table.filters[${index}]`, [
              ...value,
              ...this.handleFilters.filters
            ]);
          });
        }
      }

      this.$store.dispatch(this.getListActionName, params);
    },
    handleChange(pagination, filters, sorter) {
      const params = _.pick(pagination, ["current", "pageSize"]);

      if (!_.isEmpty(sorter) && sorter.order) {
        params.table = {
          order_by: [[sorter.field, sorter.order === "ascend" ? "ASC" : "DESC"]]
        };
      }

      this.getList(params);
    },
    handleFilters(filters) {
      this.handleFilters.filters = filters;
      this.getList({ current: 1, pageSize: 10 });
    },
    stateDisabled({ id, uid }) {
      // 以下用户不能操作禁用
      // memeber.id = 1 是系统所属用户；不操作自己
      return (
        _.indexOf(_.get(this.permission.update, "defaultFields"), "state") ===
          -1 ||
        (this.tableName === "member" && (id === 1 || uid === this.uid))
      );
    },
    delDisabled({ id, uid }) {
      // 以下用户不能操作删除
      // memeber.id = 1 是系统所属用户；不操作自己
      return (
        this.permission.del !== 1 ||
        (this.tableName === "member" && (id === 1 || uid === this.uid)) ||
        (this.tableName === "role" && id < 3)
      );
    },
    showRichTextModal(text) {
      this.richTextVisible = true;
      this.richTextContent = text;
    },
    showFileListModal(text) {
      this.fileListVisible = true;
      this.filesJson = text;
    }
  }
};
</script>
