<template>
  <a-form-model
    class="filter-form"
    v-bind="{ labelCol: { span: 7 }, wrapperCol: { span: 17 } }"
    @submit.prevent="handleSubmit"
  >
    <template v-for="(items, index) in filterItemsChunk">
      <a-row v-show="index === 0 || expand" :key="index" :gutter="24">
        <a-col v-for="item in items" :key="item.key" :md="24" :lg="12" :xxl="8">
          <a-form-model-item v-if="item.key === 'state'" :label="item.label">
            <a-select
              v-model="formData[item.key]"
              :placeholder="languageBase.please_select"
              allowClear
            >
              <a-select-option :value="1">
                {{ languageBase.enable }}
              </a-select-option>
              <a-select-option :value="0">
                {{ languageBase.disable }}
              </a-select-option>
            </a-select>
          </a-form-model-item>
          <a-form-model-item v-else :label="item.label">
            <template v-if="item.component === 'switch'">
              <a-select
                v-model="formData[item.key]"
                :placeholder="languageBase.please_select"
                allowClear
              >
                <a-select-option :value="1">
                  {{ languageBase.opened }}
                </a-select-option>
                <a-select-option :value="0">
                  {{ languageBase.closed }}
                </a-select-option>
              </a-select>
            </template>
            <template v-else-if="item.component">
              <DataEntry
                v-model="formData[item.key]"
                :componentName="item.component"
                :params="item.params"
              />
            </template>
            <a-input v-else v-model="formData[item.key]" allowClear />
          </a-form-model-item>
        </a-col>
      </a-row>
    </template>
    <a-row type="flex" align="middle" justify="end" style="margin-bottom:24px">
      <a-button
        type="primary"
        html-type="submit"
        icon="search"
        :loading="loading"
      >
        {{ languageBase.search }}
      </a-button>
      <a-button :style="{ marginLeft: '8px' }" @click="handleReset">
        {{ languageBase.reset }}
      </a-button>
      <a
        v-show="filterItemsChunk.length > 1"
        :style="{ marginLeft: '8px' }"
        @click="expand = !expand"
      >
        {{ languageBase.collapse }}
        <a-icon :type="expand ? 'up' : 'down'" />
      </a>
    </a-row>
  </a-form-model>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";
import { SID } from "@/dict";
import DataEntry from "./dataEntry/index.vue";

export default {
  components: { DataEntry },
  props: {
    loading: Boolean,
    tableName: String,
    field: {
      type: Object,
      default() {
        return;
      }
    }
  },
  data() {
    return {
      expand: false,
      formData: {},
      // 列在 languageBase 的配置
      LBDTC_RE: /^(name|state|created_at|updated_at|comment|created_by_uid|updated_by_uid)$/
    };
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values,
      languageTableColumn: store => store.languageTableColumn.values,
      allTableColumn: store => _.keyBy(store.table_column.all.data, "id"),
      allDataTyep: store => _.keyBy(store.data_type.all.data, "id")
    }),
    filterItems() {
      if (
        _.isEmpty(this.field) ||
        _.isEmpty(this.languageTableColumn) ||
        _.isEmpty(this.allTableColumn) ||
        _.isEmpty(this.allDataTyep)
      )
        return [];

      const items = [];
      const userFilter = {
        component: "user",
        params: { filters: [["systems", "=", SID]] },
        operator: "IN"
      };

      _.forEach(this.field.fields, id => {
        const column = this.allTableColumn[id];
        // 有才加。可能存在权限设置后，字段被删
        if (column) {
          let item = _.pick(this.allDataTyep[column.data_type.id], [
            "component",
            "params"
          ]);

          if (column.table.name === "member" && column.name === "uid") {
            item.component = "user";
          } else if (item.component === "datePicker") {
            item.component = "rangePicker";
          } else if (item.component === "radio") {
            item.component = "radioSelect";
          } else if (item.component === "checkbox") {
            item.component = "multipleSelect";
          } else if (/^(textarea|richText)$/.test(item.component)) {
            item.component = "input";
          }

          item.operator = item.component === "input" ? "LIKE" : "=";

          if (item.component === "user") {
            item = userFilter;
          } else if (item.component === "multipleSelect") {
            item.operator = "IN";
          }

          items.push({
            ...item,
            key: column.name,
            label: this.handleLabel(column.name)
          });
        }
      });

      _.forEach(this.field.defaultFields, key => {
        let item = {
          component: "",
          params: {},
          operator: "="
        };

        if (/^(created_at|updated_at)$/.test(key)) {
          item.component = "rangePicker";
          item.params = { format: "LLL" };
        } else if (/^(created|updated)_by_uid$/.test(key)) {
          item = userFilter;
        } else if (key === "id") {
          item.component = "inputNumber";
          item.params = { precision: 0, min: 1, max: 99999999 };
        }
        items.push({
          ...item,
          key,
          label: this.handleLabel(key)
        });
      });
      return items;
    },
    allFilterItem() {
      return _.keyBy(this.filterItems, "key");
    },
    filterItemsChunk() {
      return this.filterItems.length <= 6
        ? [this.filterItems]
        : [_.slice(this.filterItems, 0, 6), _.slice(this.filterItems, 6)];
    }
  },
  watch: {
    tableName() {
      this.formData = {};
      this.expand = false;
    }
  },
  methods: {
    handleLabel(key) {
      let lable = this.languageTableColumn[`${this.tableName}.${key}`];
      if (!lable && this.LBDTC_RE.test(key)) {
        lable = this.languageBase[key];
      }
      return lable || key;
    },
    handleSubmit() {
      let filters = [];
      _.forEach(this.formData, (value, key) => {
        if (
          !(
            value === undefined ||
            value === null ||
            value === "" ||
            (_.isArray(value) && _.isEmpty(value))
          )
        ) {
          const filter = this.createFilte(key, value);
          if (filter[0] === key) {
            filters.push(filter);
          } else {
            filters = [...filters, ...filter];
          }
        }
      });

      this.$emit("submit", filters);
    },
    createFilte(key, value) {
      const { operator, component } = this.allFilterItem[key];
      if (component === "rangePicker") {
        return [
          [key, ">=", value[0]],
          [key, "<=", value[1]]
        ];
      }
      if (operator === "LIKE") {
        value = `%${value}%`;
      }
      return [key, operator || "=", value];
    },
    handleReset() {
      this.formData = {};
      this.$emit("submit");
    }
  }
};
</script>

<style lang="stylus">
.filter-form
  .ant-rate
    display flex
</style>
