import { fileIsExist } from "@/api";
import { md5File, compressImage } from "@/utils";

export const handleFile = file =>
  new Promise((resolve, reject) => {
    if (file.size > 1 << 20) {
      resolve({
        ...file,
        status: "error",
        name: file.name + " - 文件大小不能超过1M"
      });
    } else {
      md5File(file)
        .then(hash => {
          // 服务器是否存在此文件
          fileIsExist(hash)
            .then(res => {
              if (res) {
                file.status = "done";
              }
              file.hash = hash;
              resolve(file);
            })
            .catch(reject);
        })
        .catch(reject);
    }
  });

// { file, width, height, quality }
export const handleImage = params =>
  new Promise((resolve, reject) => {
    // 压缩图片
    compressImage(params)
      .then(blob => {
        if (blob.size > 1 << 20) {
          reject(new Error("图片不能大于1MB"));
        } else {
          md5File(blob)
            .then(hash => {
              // 服务器是否存在此文件
              fileIsExist(hash)
                .then(res => {
                  if (res) {
                    blob.status = "done";
                  }

                  blob.hash = hash;
                  resolve(blob);
                })
                .catch(reject);
            })
            .catch(reject);
        }
      })
      .catch(reject);
  });
